import D3Tween from "./D3Tween";


/**3D组件基类 */
export default class D3Comp extends Laya.Script3D {
    /**自身对象 */
    self: Laya.Sprite3D;
    selfTween: D3Tween;

    onAwake() {
        this.self = this.owner as Laya.Sprite3D;
        this.selfTween = new D3Tween();
        this.init();
    }

    /**初始化 */
    init(...data: any[]) { }

    onDestroy() {
        Laya.timer.clearAll(this);
        this.selfTween.clearTween();
    }

    //tween效果
    tweenBornDo() {
        let tween = this.selfTween;
        tween.clearTween();
        this.self.transform.localScale = new Laya.Vector3(0, 0, 0);
        tween.toTween(this.self, { sca: new Laya.Vector3(1, 1, 1) }, 500, null, Laya.Ease.elasticOut)
    }
}
