import WxApi from "../Libs/WxApi";
import SoundMgr from "../Mod/SoundMgr";
import DropItem from "../Utils/DropItem";
import GameLogic from "./GameLogic";
import TraceProgressTracker from "./TraceProgressTracker";

export default class ItemComp extends TraceProgressTracker {
    static items: ItemComp[];
    child: Laya.Sprite3D;

    isTrigger: boolean = false;

    onStart(): void {
        if (!ItemComp.items) { ItemComp.items = []; }
        ItemComp.items.push(this);
    }

    onDestroy(): void {
        super.onDestroy();
        let index = ItemComp.items.indexOf(this);
        if (index != -1) {
            ItemComp.items.splice(index, 1);
        }
    }

    width: number = 2;
    length: number = 2;
    setSize(width, length) {
        this.width = width;
        this.length = length;
    }

    init(minDistance?: number, randomDistance?: number): void {
        super.init(minDistance, randomDistance);
    }

    setPos(minDistance = 0, randomDistance = 0, lPosX = 0) {
        this.init(minDistance, randomDistance);
        let child = this.self.getChildAt(0) as Laya.Sprite3D
        this.child = child;
        let lPos = child.transform.localPosition.clone();
        lPos.x = lPosX;
        child.transform.localPosition = lPos;
        this.recalcPos();
    }
    onUpdate(): void {
        if (this.self.destroyed) return;
        if (this.isTrigger) return;

        let cars = GameLogic.Share.carComps;
        for (let i = 0; i < cars.length; i++) {
            if (Math.abs(cars[i].progressDistance - this.progressDistance) < this.length) {
                if (Math.abs(cars[i].role.transform.localPosition.x - this.child.transform.localPosition.x) < this.width - 0.2) {
                    this.hit(cars[i].speedFactor);

                    if (!cars[i].isAi) {
                        if (this.self.name == "BasketBall" || this.self.name == "FootBall") {
                            SoundMgr.instance.playSoundEffect("HitBalls");
                        }
                        else if (this.self.name == "Cone") {
                            SoundMgr.instance.playSoundEffect("HitCone");
                        }
                        else {
                            SoundMgr.instance.playSoundEffect("HitWood");
                        }
                        WxApi.DoVibrate(true);
                    }
                    break;
                }
            }
        }
    }

    hit(factor) {
        if (this.isTrigger) return;
        this.isTrigger = true;
        let comp = this.self.addComponent(DropItem);
        comp.dorpDeadY = this.self.transform.position.y - 2;
        comp.isDestory = true;
        comp.dropDeadChangeX = 0.65;
        comp.isLandRotate = true;
        comp.isActive = true;
        comp.yforce = 0;
        comp.changeDropData(new Laya.Vector2(15 * factor, 30 * factor), new Laya.Vector2(-10, 10), new Laya.Vector2(10 * factor * 3, 15 * factor * 3), new Laya.Vector3(1, 1, 1), new Laya.Vector3(comp.randomMinMax(-10, 10), comp.randomMinMax(-2, 2), comp.randomMinMax(-2, 2)), 25);
    }
}