import D3Comp from "./D3Comp";
import MyVector3 from "./MyVector3";

export default class DropObj extends D3Comp {

    yForceMin = 1;
    yForceMax = 2;
    xForceMin = 0.5;
    xForceMax = 1;
    zForceMin = 15;
    zForceMax = 25;
    dir = new Laya.Vector3(0, 1, 0);
    gravity = 0;

    yforce = 0;
    xForce = 0;
    zForce = 0;
    rotate: Laya.Vector3 = new Laya.Vector3;

    dorpDeadY = 0;
    dropDeadChangeX = 4;

    isDestory = true;

    init() {
        this.setData();
    }

    changeDropData(upForce: Laya.Vector2, horizonlF: Laya.Vector2, fowardF: Laya.Vector2, dir: Laya.Vector3, rotate: Laya.Vector3, gravity: number) {
        this.yForceMin = upForce.x;
        this.yForceMax = upForce.y;
        this.xForceMin = horizonlF.x;
        this.xForceMax = horizonlF.y;
        this.zForceMin = fowardF.x;
        this.zForceMax = fowardF.y;
        this.rotate = rotate;
        this.dir = dir;
        this.gravity = gravity;
        this.setData();
    }

    setTimeDestroy(value = 1000) {
        Laya.timer.once(value, this, () => {
            this.self.destroy();
        })
    }
    
    randomMinMax(min, max) {
        return Math.random() * (max - min) + min;
    }

    setData() {
        this.xForce = this.randomMinMax(this.xForceMin, this.xForceMax);
        this.yforce = this.randomMinMax(this.yForceMin, this.yForceMax);
        this.zForce = this.randomMinMax(this.zForceMin, this.zForceMax);
    }

    isActive = true;
    onUpdate() {
        if (!this.isActive)
            return;
        let dt = Laya.timer.delta / 1000;
        //掉落效果Update
        this.yforce -= this.gravity * dt;
        let self = this.self as Laya.Sprite3D;
        if (self && !self.destroyed) {
            self.transform.translate(new Laya.Vector3(this.dir.x * this.xForce * dt, this.yforce * dt, this.dir.z * this.zForce * dt), false);
            self.transform.rotate(MyVector3.mull(this.rotate, dt), true)
            //如果超过跑道两侧则掉落
            if (Math.abs(this.self.transform.position.x) > this.dropDeadChangeX) {
                this.dorpDeadY = -19.5;
            }
            //如果超过掉落死亡线则销毁
            if (this.self.transform.position.y < this.dorpDeadY) {
                if (this.isDestory)
                    this.self.destroy();
                else {
                    this.isActive = false;
                }
            }
        }
    }
}