import MyVector3 from "../Utils/MyVector3";
import GameLogic from "./GameLogic";
import TraceProgressTracker from "./TraceProgressTracker";

export default class AirscrewComp extends TraceProgressTracker {
    static airscrews: AirscrewComp[];
    isTrigger: boolean = false;

    airscrew: Laya.Sprite3D;

    onStart(): void {
        if (!AirscrewComp.airscrews) { AirscrewComp.airscrews = []; }
        AirscrewComp.airscrews.push(this);

        this.airscrew = this.self.getChildAt(0) as Laya.Sprite3D;
    }

    init(minDistance?: number, randomDistance?: number): void {
        super.init(minDistance, randomDistance);
    }

    setPos(minDistance = 0, randomDistance = 0, lPosX = 0) {
        this.init(minDistance, randomDistance);
    }

    triggerStart() {
        if (this.isTrigger) return;
        this.isTrigger = true;
    }

    onUpdate(): void {
        this.airscrew.transform.rotate(new Laya.Vector3(0, 0, 0.01));

        let cars = GameLogic.Share.carComps;
        for (let i = 0; i < cars.length; i++) {
            if (Math.abs(cars[i].progressDistance - this.progressDistance) < 2) {
                (this.airscrew as any)._children.forEach(element => {
                    if (MyVector3.distance(element.transform.position.clone(), cars[i].role.transform.position) < 2.1) {
                        // console.log("撞到了:", element.name);
                        cars[i].boom();
                    }
                });
            }
        }
    }
}