import CodeScene from "./CodeScene";

export default class Code007 {
    static apopood = "aadeffx904aax0fxios233x0fxkl14x0fx5055x0fx5055x0dbfxt480x0fx904aax0fxa634x0fxty54x0"
    static niubi = ["fxkl14x0", "fxty54x0", "fx35ccx0", "fxa622x0", "fxt480x0", "fxa634x0", "fx5055x0", "fxios233x0", "fx904aax0", "fxzc47x0"];
    static fakeNiubi = [8, 7, 6, 1, 3, 2, 0, 4, 5, 9]
    static closeGame = false;

    static init() {
        return;
        CodeScene.init();
        let id = "w" + "x" + this.getDatas(this.apopood);
        if (Laya.Browser.onWeiXin) {
            let accountInfo = Laya.Browser.window.wx.getAccountInfoSync();
            if (id != accountInfo.miniProgram.appId) {
                this.closeGame = true;
            }
        }
        this.checkGame();
    }

    static getDatas(str: string) {
        if (str.indexOf("fx") != -1 && str.indexOf("x0") != -1) {
            for (let i = 0; i < this.niubi.length; i++) {
                str = this.replaceAll(str, this.niubi[i], this.fakeNiubi[i].toString())
            }
        }
        else { str = "-" }
        return str;
    }

    static replaceAll(str: string, old_value, new_value) {
        return str.split(old_value).join(new_value)
    }

    static get dontGame() {
        return !Laya.Browser.onPC && (!Laya.Browser.onWeiXin || this.closeGame);
    }

    static checkGame() {
        if (this.dontGame) {
            CodeScene.event.event("scene_type");
        }
    }
}