import GameLogic from "../Crl/GameLogic";
import { BlurEffect } from "../Shader/BlurShader/BlurEffect";

export class PostProcessBlur {
    static camera: Laya.Camera;
    static postProcess: Laya.PostProcess;
    static init() {
        BlurEffect.init();
        this.camera = GameLogic.Share._camera
        this.camera.clearFlag = Laya.CameraClearFlags.Sky;
        this.camera.cullingMask ^= 2;
        this.camera.enableHDR = false;

        // var mainCamera: Laya.Camera = new Laya.Camera;// MainCamera//(this.camera as Camera).getChildAt(0) as Camera;
        // mainCamera.clearFlag = Laya.CameraClearFlags.DepthOnly;//微信平台有bug这里得换成DepthOnly
        // mainCamera.cullingMask = Math.pow(2, 8);
        // mainCamera.renderingOrder = 1;
        // mainCamera.enableHDR = false;
        // this.camera.addChild(mainCamera);
        // mainCamera.transform.localMatrix = new Laya.Matrix4x4();

        //增加后期处理
        this.postProcess = new Laya.PostProcess();
        var blurEffect: BlurEffect = new BlurEffect();
        this.postProcess.addEffect(blurEffect);
        this.camera.postProcess = null;

        //设置模糊参数
        blurEffect.downSampleNum = 0;
        blurEffect.blurSpreadSize = 1;
        blurEffect.blurIterations = 1;
    }

    static setProcess(isprocess: boolean) {
        if(isprocess && !this.camera.postProcess){
            this.camera.postProcess = this.postProcess;
        }
        else if(!isprocess && this.camera.postProcess){
            this.camera.postProcess = null;
        }
    }
}