import MyVector3 from "./MyVector3";

export default class QuickTool3D {
    /**
     * 克隆对象
     * @param prefab 预制体
     * @param parent 父节点
     * @returns 
     */
    static D3CloneObj(prefab, parent?) {
        let clone = prefab.clone();
        if (parent) parent.addChild(clone);
        return clone;
    }

    static D3LoadTexture2D(url: string, callback: Function) {
        //"scene/Conventional/Assets/Projects/RobotRun/Textures/Glass.png"
        Laya.Texture2D.load(url, Laya.Handler.create(null, (tex) => {
            callback(tex);
        }));
    }

    static D3SetMaterialTexture(mat, url) {
        this.D3LoadTexture2D(url, (tex) => {
            mat.albedoTexture = tex;
        });
    }

    /**
     * 遍历rootNode的子节点，查找name匹配的节点
     * @param rootNode 根节点
     * @param name 需要查找的节点的name
     */
    static D3FindNodeByName(rootNode: Laya.Sprite3D, name: string): Laya.Sprite3D {
        let targetNode: Laya.Sprite3D = null

        let funC = (node: Laya.Sprite3D) => {
            for (let i = 0; i < node.numChildren; i++) {
                if (node.getChildAt(i).name == name) {
                    targetNode = node.getChildAt(i) as Laya.Sprite3D
                    return
                } else {
                    funC(node.getChildAt(i) as Laya.Sprite3D)
                }
            }
        }

        funC(rootNode)
        return targetNode;
    }

    /**3D转屏幕坐标 */
    static D3Vec3ToVec2(camera: Laya.Camera, targetPos: Laya.Vector3): Laya.Vector2 {
        //处理坐标在后面导致显示异常
        let dir = new Laya.Vector3;
        camera.transform.getForward(dir);
        Laya.Vector3.normalize(dir, dir);
        let v1 = MyVector3.subtract(targetPos, camera.transform.position)
        let angle = this.d3_getAngle(dir, v1);
        if (angle > 90) return null;

        var outPos = new Laya.Vector4;
        camera.viewport.project(targetPos, camera.projectionViewMatrix, outPos);
        var pos2d = new Laya.Vector2(outPos.x / Laya.stage.clientScaleX, outPos.y / Laya.stage.clientScaleY);
        return new Laya.Vector2(pos2d.x, pos2d.y);
    }

    static D3GetRgbaByHex(_hexColor) {
        var color = [], rgb = [];
        let hexColor = _hexColor.replace(/#/, "");
        if (hexColor.length == 3) { // 处理 "#abc" 成 "#aabbcc"
            var tmp = [];
            for (var i = 0; i < 3; i++) {
                tmp.push(hexColor.charAt(i) + hexColor.charAt(i));
            }
            hexColor = tmp.join("");
        }

        for (var i = 0; i < 3; i++) {
            color[i] = "0x" + hexColor.substr(i * 2, 2);
            // rgb.push(parseInt(Number(color[i])));
            rgb.push(parseInt(color[i]));
        }
        return new Laya.Vector4(rgb[0] / 255, rgb[1] / 255, rgb[2] / 255, 1);
    }

    static d3_getAngle(v1: Laya.Vector3, v2: Laya.Vector3): number {
        var a = Laya.Vector3.dot(v1, v2)
        var b = Laya.Vector3.scalarLength(v1) * Laya.Vector3.scalarLength(v2);

        if (b == 0) {
            if (v1.z > v2.z) return 0;
            else if (v1.z < v2.z) return 180;

            if (v1.y > v2.y) return 90;
            else if (v1.y < v2.y) return -90;
        }

        var cosAngle = a / b;
        // console.log(cosAngle, a, b);
        var angle = Math.acos(cosAngle) * 180 / Math.PI;
        return angle;
    }

    static getCoinPoints(pointA, pointB, throwTime = 1.8, g = -10, amount = 30, prefab, parent) {
        var points = [];

        var dTime = 0;
        var targetY = pointB.y;
        var gravity = new Laya.Vector3;

        var speed = new Laya.Vector3(-(pointB.x - pointA.x) / throwTime,
            (pointB.y - pointA.y) / throwTime - 0.5 * g * throwTime,
            (pointB.z - pointA.z) / throwTime);

        var posTmp = pointA;

        while (posTmp.y >= targetY) {
            gravity.y = g * (dTime += 0.02);

            let pos = new Laya.Vector3();
            Laya.Vector3.add(speed, gravity, pos);
            Laya.Vector3.scale(pos, 0.02, pos);

            Laya.Vector3.add(posTmp, pos, pos);
            posTmp = pos;
            points.push(pos);
        }

        let objs = this.showCoin(points, amount, prefab, parent);
        return [points, objs];
    }

    static showCoin(points, amount, prefab, parent) {
        var objs = [];
        var dis = 10;
        var count = 0;
        var offsetX = Math.random() * 200 - 100;

        var getThrowX = (a, b, c, X) => {
            return a * Math.pow(X, 2) + b * X + c;
        }

        /**创建金币 */
        var createCoinCurve = (pos: Laya.Vector3) => {
            let model = Laya.Sprite3D.instantiate(prefab);
            parent.addChild(model);
            model.transform.position = pos;
            model.transform.setWorldLossyScale(new Laya.Vector3(2, 2, 2));
            // model.addComponent(CoinComponent);
            objs.push(model);
        }

        //Math.floor(points.length / 2)
        for (var i = 0; i < points.length; i++) {
            if (count == amount) continue;

            if (i % dis == 0) {
                // var point = points[i];
                // point.x = getThrowX(offsetX, 0, 0, (point.y - points[0].y) / 500);
                createCoinCurve(points[i]);
                count++;
            }
        }
        return objs;
    }

    /**物体抖动 */
    static D3ObjectShake(target: Laya.Sprite3D, shakeTime: number = 1, shakeAmount: number = 0.7) {
        var shake = shakeTime;
        var decreaseFactor = 1;
        var originalPos = target.transform.localPosition.clone();

        Laya.timer.frameLoop(1, this, updateShake);

        function randomPos(): Laya.Vector3 {
            var x = Math.random() > 0.5 ? Math.random() : -(Math.random());
            var y = Math.random() > 0.5 ? Math.random() : -(Math.random());
            return new Laya.Vector3(x, y, 0);
        }

        function updateShake() {
            if (shake > 0) {
                var pos = new Laya.Vector3();
                Laya.Vector3.scale(randomPos(), shakeAmount, pos);
                Laya.Vector3.add(originalPos, pos, pos);
                target.transform.localPosition = pos;

                shake -= 0.02 * decreaseFactor;
            }
            else {
                shake = 0;
                target.transform.localPosition = originalPos;
                Laya.timer.clear(this, updateShake);
            }
        }
    }

    static clearMask(spr) {
        if (spr.mask) {
            spr.mask.graphics.clear();
            spr.destroy();
        }
    }

    static getAngle(p1: Laya.Vector3, p2: Laya.Vector3, p3: Laya.Vector3) {
        let v1 = MyVector3.subtract(p1, p2);
        v1.y = 0;
        let v2 = MyVector3.subtract(p2, p3);
        v2.y = 0;
        let angle = this.d3_getAngle(v1, v2);
        // console.log(angle);
        return angle;
    }

        /**秒转分:秒 */
        static getTimeLeft2BySecond(s: number, t: string = ":"): string {
            let minutes = Math.round((s - 30) / 60);// % 60; //小时制则需要%60
            let seconds = s % 60;
            let millisecond = seconds.toFixed(3).split('.')[1];
    
            var secStr = seconds > 10 ? seconds.toFixed(0) + t : "0" + seconds.toFixed(0) + t;
            if (secStr == "010" + t) { secStr = "10" + t; }
            else if (secStr == "60" + t) {
                secStr = "00" + t;
                minutes += 1;
            }
            var minStr = minutes > 9 ? minutes + t : "0" + minutes + t;
    
            return (minutes > 0 ? minStr : "00" + t) + (seconds > 0 ? (secStr + "") : "00" + t) + millisecond;
        }
}