import Utility from "../Mod/Utility"
import SoundMgr from "../Mod/SoundMgr"
import GameLogic from "../Crl/GameLogic"
import PlayerDataMgr from "../Libs/PlayerDataMgr"
import D2Tween from "../Utils/D2Tween"
import SGMgr from "../SGSDK/SGMgr"
import Strength from "../Utils/Strength"
import CommonData from "../Utils/CommonData"

export default class StartUI extends Laya.Scene {
    startBtn: Laya.Image;
    changeBtn: Laya.Image;
    driverBtn: Laya.Image;
    optionBtn: Laya.Image;
    leftBtn: Laya.Image;
    rightBtn: Laya.Image;
    carBtn: Laya.Image;

    chooseId: number = 0

    imgLight: Laya.Image;
    lightMask: Laya.Image;

    imgRedPoint: Laya.Image;
    imgGear: Laya.Image;

    imgCarTitle: Laya.Image;
    lbCarName: Laya.Label;
    lbGrade: Laya.Label;

    lbStrength: Laya.Label;
    lbStrengthTime: Laya.Label;

    imgCar: Laya.Image;
    unlockId: number = 1;
    imgExpProgress: Laya.Image;

    showLight() {
        this.imgLight.x = -270;
        this.lightMask.x = 270;

        var lightFrame = () => {
            this.lightMask.x = -this.imgLight.x;
        }

        D2Tween.toPosition(this.imgLight, { x: 350, y: this.imgLight.y }, 1000, null, () => {
            Laya.timer.clear(this, lightFrame);
            Laya.timer.once(5000, this, this.showLight);
        });
        Laya.timer.frameLoop(1, this, lightFrame);
    }

    showRedPoint() {
        this.imgRedPoint.visible = true;
        D2Tween.toScale(this.imgRedPoint, 1, 1.1, 1000, null, null, true, true);
    }

    coinNum: Laya.Label

    // guide: Guide;
    onOpened(data) {
        SoundMgr.instance.playMusic('bgm')
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        Utility.addClickEvent(this.startBtn, this, this.startBtnCB)
        Utility.addClickEvent(this.optionBtn, this, this.showOptionClick, null, false);
        Utility.addClickEvent(this.changeBtn, this, this.skinBtnCB)
        Utility.addClickEvent(this.carBtn, this, this.carBtnCB, null, false);
        Laya.timer.frameLoop(1, this, this.myUpdate)

        Utility.addClickEvent(this.leftBtn, this, this.changeCar, [false], false);
        Utility.addClickEvent(this.rightBtn, this, this.changeCar, [true], false);

        Utility.addClickEvent(this.driverBtn, this, this.driverBtnCB)

        this.carBtn.visible = false;
        for (let i = 1; i < 10; i++) {
            if (PlayerDataMgr.getPlayerData().skinArr[i] == 0) {
                this.carBtn.visible = true;
                this.imgCar.skin = "startUI/Car_" + (i + 1) + ".png";
                this.unlockId = i;

                let myExp = PlayerDataMgr.getPlayerData().exp;
                let needExp = 1600 + (this.unlockId - 1) * 1600;
                this.imgExpProgress.width = 406 * Math.min(1, (myExp / needExp));
                break;
            }
        }

        // let g = PlayerDataMgr.getPlayerData().grade;
        // this.levelMask.x = ((((g - 1) % 5)) / 5) * 286 - 286 + (286 * 0.2);

        // this.updateData();

        // D2Tween.toAlpha(this.startBtn.getChildAt(0) as Laya.Image, 1, 0.5, 1000, null, null, true, true);

        // if (PlayerDataMgr.getPlayerData().grade == 2 && PlayerDataMgr.getPlayerData().skinLvl[0] == 1) {
        //     this.guide = new Guide();
        // }
        Laya.timer.once(5000, this, this.showLight);
        SGMgr.inHome();

        this.chooseId = PlayerDataMgr.getPlayerData().skinId;
        this.selectCar();

        if (data) {
            if (data.openUpgrade == 1) {
                Laya.timer.once(200, this, () => {
                    this.skinBtnCB();
                });
            }
            else if (data.openUpgrade == 2) {
                Laya.timer.once(200, this, () => {
                    this.driverBtnCB();
                });
            }
        }
    }

    changeCar(args) {
        this.chooseId += (args[0] ? 1 : -1);
        this.selectCar();
    }

    selectCar() {
        if (this.chooseId < 0) this.chooseId = 9;
        else if (this.chooseId > 9) this.chooseId = 0;
        // this.imgLock.visible = this.curTab == Tabs.upgradeTab && PlayerDataMgr.getPlayerData().skinArr[this.chooseId] == 0;
        // this.imgUnlock.visible = this.curTab == Tabs.upgradeTab && !this.imgLock.visible;
        // console.log(this.chooseId)

        let data = PlayerDataMgr.carConfig[this.chooseId];
        this.lbCarName.text = data.CarName + " Lv" + PlayerDataMgr.getPlayerData().skinLvl[this.chooseId];
        this.lbGrade.text = data.grade;
        this.imgCarTitle.skin = "startUI/ksbs_dk_clsx_" + data.level + ".png";

        GameLogic.Share.updateSkin(this.chooseId);
    }

    // imgLevel:Laya.Image;
    // imgUp1:Laya.Image;
    // imgUp2:Laya.Image;
    // imgUp3:Laya.Image;
    onClosed() {
        Laya.timer.clearAll(this)

        // QuickTool3D.clearMask(this.imgLevel);
        // QuickTool3D.clearMask(this.imgUp1);
        // QuickTool3D.clearMask(this.imgUp2);
        // QuickTool3D.clearMask(this.imgUp3);
    }

    startBtnCB() {
        Strength.Share.subStrength((canPlay) => {
            if (canPlay) {
                SGMgr.startGame(() => {
                    // Laya.Scene.open('MyScenes/GameUI.scene')
                    Laya.Scene.open('MyScenes/MatchUI.scene')
                })
            }
            else {
                Laya.Scene.open('MyScenes/StrengthUI.scene', false)
                SGMgr.inShop();
            }
        });

    }

    showOptionClick() {
        Laya.Scene.open("MyScenes/OptionUI.scene", false);
    }

    skinBtnCB() {
        // FdAd.visibleSideGridAd(false)
        // // FdAd.visibleTopGrid(false)
        // if (FdMgr.banner_gezi_switch) {
        //     FdAd.hideBannerAd()
        // } else {
        //     FdAd.visibleBottomGridAd(false)
        // }
        // GameLogic.Share._levelNode.active = false
        // GameLogic.Share._scene.active = false;
        Laya.Scene.open('MyScenes/SkinUI.scene')
        SGMgr.inShop();
    }

    carBtnCB() {
        Laya.Scene.open("MyScenes/ExpUI.scene", true, { carId: this.unlockId });
        SGMgr.inShop();
    }

    driverBtnCB() {
        Laya.Scene.open('MyScenes/DriverUI.scene', false)
        SGMgr.inShop();
    }

    imgDriver: Laya.Image;
    lbName: Laya.Label;
    driverId: number = 0;
    myUpdate() {
        this.coinNum.text = PlayerDataMgr.getPlayerData().coin.toString();
        this.imgGear.rotation += 0.5;

        this.lbStrengthTime.visible = Strength.Share.currentStrength < Strength.Share.maxStrength;
        this.lbStrength.text = Strength.Share.currentStrength + "/" + Strength.Share.maxStrength;
        if (this.lbStrengthTime.visible && Strength.Share.time) {
            this.lbStrengthTime.text = Strength.Share.time;
        }

        if(CommonData.tryDriverId != 0){
            let data = PlayerDataMgr.driverUpgradeData[CommonData.tryDriverId- 1];
            this.imgDriver.skin = "driverUI/ksbs_img_tx" + CommonData.tryDriverId+ ".png";
            this.lbName.text = data.name + "(试用)";
        }
        else{
            let id = PlayerDataMgr.getPlayerData().driverId;
            if (this.driverId != id) {
                this.imgDriver.skin = "driverUI/ksbs_img_tx" + id + ".png";
                this.driverId = id;
                this.lbName.text = PlayerDataMgr.driverUpgradeData[id - 1].name;
            }
        }
    }
}
