    init(minDistance?: number, randomDistance?: number): void {
        if (minDistance < 20) minDistance = 20;

        this.touchId = null;
        PlayerCrl.share = this;
        this.curRoadIndex = -1;
        this.setRoadConfig(0);
        this.canMove = true;
        this.isPressed = true;
        this.speedFactor = 0;
        this.isFinish = false;

        if (this.role) {
            this.targetPos = new Laya.Vector3;
            this.targetRot = new Laya.Vector3;
        }
        else {
            this.role = this.self.getChildByName("Car_1") as Laya.Sprite3D;

            if (!this.isAi) {
                this.camera = this.self.getChildByName("Camera") as Laya.Sprite3D;
                // Laya.stage.on(Laya.Event.MOUSE_DOWN, this, (e) => {
                //     this.onMouseEvent({ type: "down", evt: e });
                // });

                // Laya.stage.on(Laya.Event.MOUSE_UP, this, (e) => {
                //     this.onMouseEvent({ type: "up", evt: e });
                // });

                // Laya.stage.on(Laya.Event.MOUSE_OUT, this, (e) => {
                //     this.onMouseEvent({ type: "out", evt: e });
                // });

                // Laya.stage.on(Laya.Event.MOUSE_MOVE, this, (e) => {
                //     this.onMouseEvent({ type: "move", evt: e });
                // });

                Laya.timer.frameLoop(1, this, this.frameUpdateRolePos);
                this.realCamera = this.camera.getChildByName("Main Camera") as Laya.Camera;
                this.speedLineFx = (this.realCamera.getChildByName("SpeedLine_1") as Laya.Sprite3D)
                this.camera.addChild(GameLogic.Share._light);
                this.camOriPos = this.camera.transform.position.clone();
                this.camOriRot = this.camera.transform.rotationEuler.clone();
            }
        }
        super.init(minDistance, randomDistance);

        if (!this.isAi) {
            this.newFov = 60;
            this.realCamera.fieldOfView = 60;
            this.camera.transform.localPosition = new Laya.Vector3(0, 3, -9);
            this.camera.transform.localRotationEuler = new Laya.Vector3(10, 0, 0);
            this.speedLineFx.active = false;

            // this.maxSpeed = PlayerDataMgr.getPlayerData().skinLvl[PlayerDataMgr.getPlayerData().skinId] * 0.2 + 2.8;;

            let carUpgradeData = PlayerDataMgr.carUpgradeData[this.carId - 1];
            let carLevel = PlayerDataMgr.getPlayerData().skinLvl[this.carId - 1];
            this.ctrlUpgradeFactor = GameLogic.Share.getUpgradeData(carUpgradeData.ctrl, carUpgradeData.addCtrl, carLevel) * 0.9;
            this.spdUpgradeFactor = GameLogic.Share.getUpgradeData(carUpgradeData.spd, carUpgradeData.addSpd, carLevel);
            this.acceUpgradeFactor = GameLogic.Share.getUpgradeData(carUpgradeData.acce, carUpgradeData.addAcce, carLevel);

            this.maxSpeed = 2.8 * this.spdUpgradeFactor * 0.7;
            this.acce = 0.001 * this.acceUpgradeFactor * 0.8;
        }

        this.role.transform.localRotationEuler = new Laya.Vector3;
        this.roleChild = this.role.getChildAt(0) as Laya.Sprite3D;
    }

    trailL: Laya.Sprite3D;
