    createTrail() {
        if (!this.trailL) {
            this.trailL = QuickTool3D.D3CloneObj(GameLogic.Share._prefab.getChildByName("DriftTrial"));
            this.roleChild.addChild(this.trailL);
            this.trailL.transform.localPosition = new Laya.Vector3(-0.528, 0.2, -1.437);
            SoundMgr.instance.playSoundEffect('Drift');
        }
        if (!this.trailR) {
            this.trailR = QuickTool3D.D3CloneObj(GameLogic.Share._prefab.getChildByName("DriftTrial"));
            this.roleChild.addChild(this.trailR);
            this.trailR.transform.localPosition = new Laya.Vector3(0.528, 0.2, -1.437);
        }
    }

    destroyTrail() {
        if (this.trailL) {
            this.trailL.destroy();
            this.trailL = null;
            SoundMgr.instance.stopSound('Drift');
        }
        if (this.trailR) {
            this.trailR.destroy();
            this.trailR = null;
        }
    }

    onUpdate(): void {
        if (!GameLogic.Share.isStartGame) return;
        if (this.isBoom) return;
        if (!this.canMove) return;

        //test
        // this.onMouseEvent({ type: "move", evt: {stageX: 0, stageY:0, touchId: 0} });

        super.onUpdate();
        this.speedChange();

        if (this.isLeft) {
            this.moveLeft();
        }
        else if (this.isRight) {
            this.moveRight();
        }
        else {
            this.cancelLR();
        }


        let pos = this.role.transform.localPosition.clone();
        pos.y = this.getY(pos) + this.jumpY;
        Laya.Vector3.lerp(this.role.transform.localPosition.clone(), pos, 0.3, pos);
        this.role.transform.localPosition = pos;
        if (!this.isJump) {
            this.ctrlCarLR(pos);
        }

        if (!this.isAi) {
            let pos2 = this.realCamera.transform.localPosition.clone();
            pos2.x = pos.x * 0.8;
            pos2.y = pos.y;
            Laya.Vector3.lerp(this.realCamera.transform.localPosition, pos2, 0.6, pos2);
            this.realCamera.transform.localPosition = pos2;

            if (this.isDrift) {
                //获取漂移角度
                let data = this.circuit.getRouteAngle(this.progressDistance + this.lookAheadForTargetOffset * this.speedFactor + this.lookAheadForTargetFactor * this.speed)
                let angle = data[0];
                let isLeft = data[1];

                let rot = this.roleChild.transform.localRotationEuler.clone();
                let factor = 1;

                let maxAngle = 30;
                let moveFactor = 0.05;

                if (angle > 0.05) {
                    if (isLeft) {
                        var targetY = rot.y + factor;
                        if (targetY > maxAngle) {
                            targetY = maxAngle;
                        }

                        this.targetPos.x -= moveFactor;
                    }
                    else {
                        targetY = rot.y - factor;
                        if (targetY < -maxAngle) {
                            targetY = -maxAngle;
                        }

                        this.targetPos.x += moveFactor;
                    }
                    rot.y = targetY;

                    GameUI.Share.curNos -= 1;
                    if (GameUI.Share.curNos < 28) GameUI.Share.curNos = 28;

                    this.createTrail();
                }
                else {
                    if (rot.y > 0) {
                        targetY = rot.y - factor;
                        if (targetY < 0) targetY = 0;
                    }
                    else if (rot.y < 0) {
                        targetY = rot.y + factor;
                        if (targetY > 0) targetY = 0;
                    }
                    else {
                        targetY = 0;
                    }
                    rot.y = targetY;
                    this.destroyTrail();
                }
                // Laya.Vector3.lerp(this.roleChild.transform.localRotationEuler.clone(), rot, 0.5, rot);
                this.roleChild.transform.localRotationEuler = rot;
                // console.log(rot.y);
            }
            else {
                this.destroyTrail();
            }
        }

        if (this.curRoadIndex == -1) return;
        let configs = GameLogic.Share.configPaths;
        for (let i = configs.length - 1; i >= 0; i--) {
            if (this.progressDistance > configs[i][2]) {
                if (i == configs.length - 1) {//终点
                    this.isPressed = false;
                    this.speedTimer = 0;
                    this.oldSpeed = this.speedFactor;
                    this.curRoadIndex = configs[i][1];
                    this.isFinish = true;

                    if (!this.isAi) {
                        this.cameraTween();
                        GameLogic.Share.finishFX();
                        let cars = GameLogic.Share.carComps.sort((a, b) => { return b.progressDistance - a.progressDistance });
                        GameLogic.Share.myRank = cars.indexOf(this) + 1;

                        Laya.timer.once(2000, this, () => {
                            // GameLogic.Share.gameOver(true);
                            GameLogic.Share.gameOver(GameLogic.Share.myRank == 1);
                        });
                    }
                    break;
                }
                else {
                    this.setRoadConfig(configs[i][1]);
                    break;
                }
            }
        }
    }

