    cameraTween() {
        // let tween = new Tween3D;
        // tween.toTween(this.camera, {pos:})

        this.speedFactor = 2.5;
        this.oldSpeed = 2.5;
        this.camera.transform.localPosition = new Laya.Vector3(-3.6, 0.8, 13);
        let pos = this.camera.transform.position.clone();
        Laya.timer.frameLoop(1, this, this.lookAtCar, [pos])
    }

    lookAtCar(pos) {
        this.camera.transform.lookAt(this.self.transform.position, new Laya.Vector3(0, 1, 0), false);
        let rot = this.camera.transform.rotationEuler.clone();
        rot.y += 180;
        rot.x *= -1;
        rot.z = 0;
        this.camera.transform.rotationEuler = rot;
        this.camera.transform.position = pos;
    }

