    lrTimer: number = 0;
    movelrFactor: number = 0.08;
    maxlrDis: number = 10;
    moveRight() {
        this.lrTimer += Laya.timer.delta;
        let dis = this.lrTimer * this.movelrFactor + this.originPosX;
        if (dis > this.maxlrDis) dis = this.maxlrDis;

        // console.log(dis);

        let posLimit = this.maxPosX;
        let rotLimit = this.maxRotX;

        let pos = this.role.transform.localPosition.clone();
        pos.x -= this.moveFactor * dis * Math.max(this.speedFactor, 0.6) * this.ctrlUpgradeFactor; //调整灵敏度，数字越大灵敏度越高

        if (pos.x > posLimit) pos.x = posLimit;
        else if (pos.x < -posLimit) pos.x = -posLimit;

        if (!this.isJump && !this.isDrift) {
            let rot = this.roleChild.transform.localRotationEuler.clone();
            rot.y -= dis / rotLimit;
            this.roleChild.transform.localRotationEuler = rot;
        }
        this.originPosX = this.lrTimer * 0.001;
        this.targetPos = pos;
    }

    moveLeft() {
        this.lrTimer += Laya.timer.delta;
        let dis = -this.lrTimer * this.movelrFactor + this.originPosX;
        if (dis < -this.maxlrDis) dis = -this.maxlrDis;

        let posLimit = this.maxPosX;
        let rotLimit = this.maxRotX;

        let pos = this.role.transform.localPosition.clone();
        pos.x -= this.moveFactor * dis * Math.max(this.speedFactor, 0.6) * this.ctrlUpgradeFactor; //调整灵敏度，数字越大灵敏度越高

        if (pos.x > posLimit) pos.x = posLimit;
        else if (pos.x < -posLimit) pos.x = -posLimit;

        if (!this.isJump && !this.isDrift) {
            let rot = this.roleChild.transform.localRotationEuler.clone();
            rot.y -= dis / rotLimit;
            this.roleChild.transform.localRotationEuler = rot;
        }
        this.originPosX = this.lrTimer * 0.001;
        this.targetPos = pos;
    }

    cancelLR() {
        this.lrTimer = 0;
    }

