    curCameraX: number = 0;
    frameUpdateRolePos() {
        if (GameLogic.Share.isGameOver) return;
        if (this.isJump) return;

        let curPos = this.role.transform.localPosition.clone();
        this.targetPos.setValue(this.targetPos.x, this.role.transform.localPosition.y, this.role.transform.localPosition.z);
        if (this.targetPos.x < -this.maxPosX) this.targetPos.x = -this.maxPosX;
        else if (this.targetPos.x > this.maxPosX) this.targetPos.x = this.maxPosX;

        let step = Math.abs(curPos.x - this.targetPos.x)
        if (step > 0.1) {
            step = Math.min(step, 1);
            if (curPos.x < this.targetPos.x) {
                this.moveLR(true, step);
            }
            else if (curPos.x > this.targetPos.x) {
                this.moveLR(false, step);
            }
        }

        // Laya.Vector3.lerp(this.role.transform.localPosition, this.targetPos, 0.4, this.lerpPos);
        // this.lerpPos.y = this.getY(this.lerpPos);
        // this.role.transform.localPosition = this.lerpPos;

        // let pos = this.realCamera.transform.localPosition.clone();
        // // pos.x = this.lerpPos.x * 0.3;
        // pos.x = this.lerpPos.x * 0.8;
        // pos.y = this.lerpPos.y;
        // Laya.Vector3.lerp(this.realCamera.transform.localPosition, pos, 0.6, pos);
        // this.realCamera.transform.localPosition = pos;

        if (Math.abs(this.lerpPos.x - this.targetPos.x) < 0.01) {
            this.maxDis = 0;
        }

        if (!this.isDrift) {
            this.targetRot.setValue(this.roleChild.transform.localRotationEuler.x, this.targetRot.y, this.roleChild.transform.localRotationEuler.z);
            Laya.Vector3.lerp(this.roleChild.transform.localRotationEuler, this.targetRot, 0.05, this.lerpRot);
            this.roleChild.transform.localRotationEuler = this.lerpRot;
        }

        // if (!this.isPressed && Math.abs(this.lerpRot.y) < 0.01) {
        //     Laya.timer.clear(this, this.frameUpdateRolePos);
        // }

        // this.ctrlCarLR(this.lerpPos, this.lerpRot);
    }

