    isJump: boolean = false;
    jumpY: number = 0;
    jump(index) {
        if (this.isJump) return;
        this.isJump = true;
        let acce = 0.0005;
        let speed = 3.5;
        let time = 0;

        this.oldSpeed = this.speedFactor;


        if (this.isJump) {
            let rot = this.getRot(this.role.transform.localPosition, index);
            let tween = new D3Tween;
            tween.toTween(this.role, { rot: rot }, 100, null, null, true);
        }

        let jumpDown = () => {
            this.speedFactor = this.oldSpeed;
            time += Laya.timer.delta;
            this.jumpY += speed;
            speed = 0.1 - acce * time;
            if (this.jumpY < 0) {
                let fx = GameLogic.Share.getFx("SparkFX");
                this.role.addChild(fx);

                fx.transform.setWorldLossyScale(new Laya.Vector3(1.8, 1.8, 1.8));
                fx.transform.localPosition = new Laya.Vector3(0, 0.15, -0.1);// = this.role.transform.position.clone();

                this.jumpY = 0;
                Laya.timer.clear(this, jumpDown)
                this.isJump = false;
                // this.isTween = false;
            }
        }

        let jumpUp = () => {
            this.speedFactor = this.oldSpeed;

            time += Laya.timer.delta;
            this.jumpY += speed;
            speed = 0.1 - acce * time;
            if (speed < 0) {
                speed = 0;
                time = 0;
                Laya.timer.clear(this, jumpUp);
                Laya.timer.frameLoop(1, this, jumpDown);
            }
        }

        Laya.timer.frameLoop(1, this, jumpUp);
    }


    // /**赛道配置 roadWidth:赛道宽度、 roadType:0_直线、1_椭圆、2_圆 */
