    initScene() {
        Laya.Scene3D.load(WxApi.UnityPath + 'SampleScene_2.ls', Laya.Handler.create(this, (s) => {
            this.loadScene = s;
            this.onLoadScene(s);
        }));
    }

    loadRoads() {
        for (let i = 1; i < CommonData.LevelCount + 1; i++) {
            this.loadRoad(i, (road) => {
                this.roadPrefabs.push(road);
                if (this.roadPrefabs.length == CommonData.LevelCount) {
                    if (this.g != null) {
                        wx.hideLoading();
                        this.createRoad(this.g);
                        this.loadImg.destroy();
                    }
                }
            })
        }
    }

    roadPrefabs: Laya.Sprite3D[] = [];
    loadRoad(roadId, cb) {
        let url = "Laya_lh/Conventional/Road" + roadId + ".lh"
        let resource = [
            { url: url, clas: Laya.Sprite3D, priority: 2 },
        ]
        Laya.loader.create(resource,
            Laya.Handler.create(this, () => {
                cb(Laya.loader.getRes(url));
            }, [{ target: "3dRes" }], false), null);
    }

    setShadow(light, mode = Laya.ShadowMode.SoftHigh, resolution = 2048, cascade = Laya.ShadowCascadesMode.FourCascades) {
        // Use soft shadow.
        light.shadowMode = mode;
        // Set shadow max distance from camera.
        light.shadowDistance = 15;
        // Set shadow resolution.
        light.shadowResolution = resolution;
        // Set shadow cascade mode.
        light.shadowCascadesMode = cascade;
        // Set shadow normal bias.
        light.shadowNormalBias = 0;
    }

    onLoadScene(scene) {
        if (!this.isInitSDK || !this.loadScene) return;

        Laya.Scene.open('MyScenes/StartUI.scene')

        this._scene = Laya.stage.addChild(scene) as Laya.Scene3D
        Laya.stage.setChildIndex(this._scene, 0)
        this._camera = QuickTool3D.D3FindNodeByName(scene, "Main Camera") as Laya.Camera// this._scene.getChildByName('Main Camera') as Laya.Camera
        this._light = this._scene.getChildByName('Directional Light') as Laya.DirectionLight
        // this.setShadow(this._light);

        // //雾化代码
        // this._scene.enableFog = false;
        // //设置雾化的颜色
        // this._scene.fogColor = new Laya.Vector3(0, 0.8, 0);
        // //设置雾化的起始位置，相对于相机的距离
        // this._scene.fogStart = 10;
        // //设置雾化最浓处的距离。
        // this._scene.fogRange = 60;

        // this.camStartPos = this._camera.transform.position.clone()
        // this.camStartRotation = this._camera.transform.rotation.clone()
        // this._camera.fieldOfView = this.startCamField

        this._prefab = scene.getChildByName("Prefab");
        this._prefab.active = false;

        this._parent = scene.getChildByName("Parent");

        this.garage = scene.getChildByName("Parking")
        this.upgradeFX = this.garage.getChildAt(0) as Laya.Sprite3D;
        this.upgradeFX.active = false;
        this.cars = scene.getChildByName("Cars");
        this.wheels = this._prefab.getChildByName("Wheels") as Laya.Sprite3D;
        this.tails = this._prefab.getChildByName("Tails") as Laya.Sprite3D;
        // this.processCars = scene.getChildByName("ProcessCars");

        let player = scene.getChildByName("Player");
        this.playerAnim = player.getComponent(Laya.Animator);

        this.nosSpdFx = player.getChildByName("Car_1").getChildByName("NosSpdFX") as Laya.Sprite3D;
        this.nosSpdFx.active = false;

        QuickTool3D.D3FindNodeByName(scene, "SpeedLine_1").active = false;

        // this._cameraCrl = this._camera.addComponent(CameraCrl)
        // this._scene.getChildByName("Plane_1").addComponent(Water)
        // this._levelNode = this._scene.addChild(new Laya.Sprite3D()) as Laya.Sprite3D

        this.showGarage(true);

        // PostProcessBlur.init();
    }

