    addComp(obj, type) {
        if (type.indexOf("Hammer") != -1) {
            return obj.addComponent(HammerComp);
        }
        else if (type == "Tonnel") {
            return obj.addComponent(TonnelComp);
        }
        else if (type == "Hurdle") {
            return obj.addComponent(HurdleComp);
        }
        else if (type == "Torture") {
            return obj.addComponent(TortureComp);
        }
        else if (type == "Airscrew") {
            return obj.addComponent(AirscrewComp);
        }
        else if (type == "Jump" || type.indexOf("Accelerator") != -1) {
            return obj.addComponent(JumpComp);
        }
        else if (type == "Tyres") {
            var comp: ItemComp = obj.addComponent(ItemComp);
            comp.setSize(2, 2);
            return comp;
        }
        else if (type == "BasketBall" || type == "FootBall") {
            comp = obj.addComponent(ItemComp);
            comp.setSize(2.5, 2.5);
            return comp
        }
        else if (type == "Cone") {
            comp = obj.addComponent(ItemComp);
            comp.setSize(1.8, 1.8);
            return comp
        }
        else if (type == "Skittle") {
            comp = obj.addComponent(ItemComp);
            comp.setSize(1.6, 1.6);
            return comp
        }
        else if (type == "Gas") {
            comp = obj.addComponent(GasComp);
            comp.setSize(2, 2);
            return comp;
        }
        else if (type == "Chest") {
            comp = obj.addComponent(ChestComp);
            comp.setSize(2, 2);
            return comp;
        }
        else if (type == "Money") {
            comp = obj.addComponent(MoneyComp);
            comp.setSize(2, 2);
            return comp;
        }
        else {
            return obj.addComponent(CommonComp);
        }
    }

    finishFX() {
        let finish: any = this._parent.getChildByName("Finish").getChildAt(0);
        finish._children.forEach(element => {
            let fx = this.getFx("FireworksFX", -1);
            fx.transform.position = element.transform.position.clone();
            fx.transform.rotationEuler = element.transform.rotationEuler.clone();
        });

        SoundMgr.instance.playSoundEffect("Crowd");
        SoundMgr.instance.playSoundEffect("Fireworks");

        PostProcessBlur.setProcess(false);
    }

    getFx(name, time = 2000) {
        let fx = QuickTool3D.D3CloneObj(this._prefab.getChildByName(name));
        this._parent.addChild(fx);
        if (time != -1) {
            Laya.timer.once(time, this, () => { fx.destroy(); });
        }
        return fx;
    }

