    createLevel() {
        this.carComps = [];
        this.showGarage(false);
        this._parent.destroyChildren();

        let g: number = PlayerDataMgr.getPlayerData().grade
        g = Math.floor((g - 1) % CommonData.LevelCount) + 1;

        let roads = this.roadPrefabs.filter(w => { return w.name == "Road" + g });
        if (roads.length == 0) {//
            this.loadImg = new Laya.Image("loadingUI/jz_bj_1.jpg");
            GameUI.Share.addChild(this.loadImg);
            this.g = g;
            //未加载完
            wx.showLoading({
                title: "资源加载中...",
                mask: true,
                success: function (): void {
                },
                fail: function (): void {
                },
                complete: function (): void {
                }
            });
        }
        else {
            this.createRoad(g);
        }
    }

    playerAnim: Laya.Animator;
