    createRoad(g) {
        let road = QuickTool3D.D3CloneObj(this.roadPrefabs.filter(w => { return w.name == "Road" + g })[0], this._parent);
        road.transform.position = new Laya.Vector3;
        let circuit: TraceCircuit = road.addComponent(TraceCircuit);

        //取点
        let path = road.getChildByName("Path");
        let configPaths = [];
        path._children.forEach(element => {
            if (element.name.indexOf("_") != -1) {
                let type = element.name.split("_");
                if (element.name != "Point_End") {
                    configPaths.push([path._children.indexOf(element), Number(type[1])]);
                }
                else {
                    configPaths.push([path._children.indexOf(element), -1]);
                }
            }
            circuit.allPointVec3.push(element.transform.position.clone());
            circuit.allRotVec3.push(element.transform.rotationEuler.clone())
        });
        path.destroy();
        circuit.init();

        for (let i = 0; i < configPaths.length; i++) {
            configPaths[i][2] = circuit.distances[configPaths[i][0]];

            // if (i < configPaths.length - 1) {
            //     let type = 0
            //     let obj = QuickTool3D.D3CloneObj(this._prefab.getChildByName("Accelerator_" + configPaths[i - 1][1]), this._parent);
            //     let comp = this.addComp(obj, "Accelerator_" + configPaths[i][1]);
            //     comp.circuit = circuit;
            //     comp.setPos(configPaths[i][2], 0);
            // }
        }
        this.configPaths = configPaths;
        console.log(configPaths);

        let levelConfig = CommonData.LevelConfig[g - 1];
        let offset = CommonData.LevelOffset[g - 1];

        //自己
        let player = this._scene.getChildByName("Player");
        let carP = player.getChildByName("Car_1");
        this.createCar(PlayerDataMgr.getPlayerData().skinId, circuit, 20 + offset, -2, false, player, carP); //

        //AI
        // let ids = [0, 1, 2, 3, 4, 5];
        // ids.splice(ids.indexOf(PlayerDataMgr.getPlayerData().skinId), 1);
        // ids = ids.sort((a, b) => {
        //     return Math.random() - 0.5;
        // });
        let ids = this.aiIds;
        // this.createCar(ids[0], circuit, 20, 2, true);
        for (let i = 0; i < 5; i++) {
            if (i == 0) {
                this.createCar(ids[i], circuit, 20 + offset, 2, true);
            }
            else {
                this.createCar(ids[i], circuit, 20 + 10 * Math.ceil(i / 2) + offset, i % 2 == 0 ? -2 : 2, true);
            }
        }

        // let airscrew = QuickTool3D.D3CloneObj(this._prefab.getChildByName("Airscrew"));
        // this._parent.addChild(airscrew);
        // let comp:AirscrewComp = airscrew.addComponent(AirscrewComp);
        // comp.circuit = circuit;
        // comp.init(1500, 0);

        for (let i = 0; i < levelConfig.length; i++) {
            let config = levelConfig[i];
            for (let j = 0; j < config.amount; j++) {
                let obj = QuickTool3D.D3CloneObj(this._prefab.getChildByName(config.type), this._parent);
                let comp = this.addComp(obj, config.type);
                comp.circuit = circuit;
                comp.setPos(config.distance + offset + config.nextOffset.dis * j, 0, config.offsetX + config.nextOffset.x * j);
            }
        }

        Laya.timer.frameLoop(1, this, () => {
            let cars = this.carComps.sort((a, b) => { return a.progressDistance - b.progressDistance });
            for (let i = 0; i < cars.length - 1; i++) {
                let car1 = cars[i];
                for (let j = 0; j < cars.length; j++) {
                    let car2 = cars[j];
                    if (car1 === car2 || car2.isBoom) continue;
                    let fbDis = Math.abs(car1.progressDistance - car2.progressDistance);
                    // console.log("fb:", fbDis);
                    if (fbDis > 3) continue;
                    let lrDis = Math.abs(car1.role.transform.localPosition.x - car2.role.transform.localPosition.x);
                    // console.log("lr:", lrDis)
                    if (lrDis > 1.6) continue;

                    car1.hit(car1.role.transform.localPosition.x <= car2.role.transform.localPosition.x, car1.progressDistance <= car2.progressDistance);
                    car2.hit(car1.role.transform.localPosition.x > car2.role.transform.localPosition.x, car1.progressDistance > car2.progressDistance);
                    break;
                }
            }
        });
    }


    createCar(id, circuit, dis, posX, isAi = false, carParent?, parent?) {
        let car = QuickTool3D.D3CloneObj(this.cars.getChildAt(id)) //isAi ?  : this.processCars.getChildAt(id));
        car.active = true;
        if (!isAi) {
            parent.addChild(car);
            var playerComp: PlayerCrl = carParent.getComponent(PlayerCrl);
            if (playerComp) {
                playerComp.destroy();
            }
            playerComp = carParent.addComponent(PlayerCrl);
            this.playerComp = playerComp;
        }
        else {
            carParent = new Laya.Sprite3D("Ai");
            this._parent.addChild(carParent);
            let parent = new Laya.Sprite3D("Car_1");
            carParent.addChild(parent);
            parent.addChild(car)
            playerComp = carParent.addComponent(AiPlayerCrl);
        }
        car.parent.setChildIndex(car, 0);
        car.transform.localPosition = new Laya.Vector3;
        car.transform.localRotationEuler = new Laya.Vector3;

        playerComp.circuit = circuit;
        playerComp.isAi = isAi;
        playerComp.carId = id + 1;
        playerComp.init(dis, 0);
        playerComp.setPosX(posX)
        this.carComps.push(playerComp);

        if (isAi) {
            this.createWheel(car, Math.ceil(Math.random() * 6));
            this.createTail(car, Math.ceil(Math.random() * 10));
            this.createColor(car, id, Math.ceil(Math.random() * 10));
        }
        else {
            this.createWheel(car, PlayerDataMgr.getPlayerData().wheelId[id]);
            this.createTail(car, PlayerDataMgr.getPlayerData().tailId[id]);
            this.createColor(car, id, PlayerDataMgr.getPlayerData().colorId[id]);
        }
    }

    createWheel(car, id) {
        for (let i = 3; i < 7; i++) {
            let wheel = QuickTool3D.D3CloneObj(this.wheels.getChildAt(id - 1));
            let wheelPoint = car.getChildAt(i);
            wheelPoint.destroyChildren();
            wheelPoint.addChild(wheel);
            wheel.transform.localPosition = new Laya.Vector3;
            wheel.transform.localRotationEuler = new Laya.Vector3;
            wheel.transform.localScale = new Laya.Vector3(1, 1, 1);
        }
    }

    createTail(car, id) {
        let tailPoint = car.getChildByName("Tail");
        tailPoint.destroyChildren();
        if (id != 0) {
            let tail = QuickTool3D.D3CloneObj(this.tails.getChildAt(id - 1), tailPoint);
            tail.transform.localPosition = new Laya.Vector3;
            tail.transform.localRotationEuler = new Laya.Vector3;
            tail.transform.localScale = new Laya.Vector3(1, 1, 1);
        }
    }

    createColor(car, carId, id) {
        let carBodyMat = car.getChildAt(0).meshRenderer.material;
        let color = PlayerDataMgr.carConfig[carId]["CarColor" + id];
        let v4 = QuickTool3D.D3GetRgbaByHex(color);
        // console.log(color);
        carBodyMat.albedoColor = v4;
    }

