    nosCount: number = 0;
    isStart: boolean = false
    touchStart(evt: Laya.Event) {
        if (GameLogic.Share.isGameOver) return
        if (GameLogic.Share.isFinish) {
            return
        }
        if (!GameLogic.Share.isStartGame && !this.isStart) {
            this.imgTip.visible = false
            GameLogic.Share.gameStart()
            this.isStart = true;
        }
        // let x = evt.stageX
        // GameLogic.Share._playerCrl.touchX = x

        if (this.imgNosLight.visible) {
            this.nosCount++;
            if (this.nosCount > 1) {
                this.useNos();
            }

            Laya.timer.once(500, this, () => {
                this.nosCount = 0;
            })
        }
    }
    touchMove(evt: Laya.Event) {
        if (GameLogic.Share.isGameOver) return
        if (GameLogic.Share.isFinish) {
            return
        }
        // let x = evt.stageX
        // GameLogic.Share._playerCrl.touchX = x
    }
    touchEnd(evt: Laya.Event) {
        if (GameLogic.Share.isGameOver) return
        if (GameLogic.Share.isFinish) {
            return
        }
    }

    showSpeed(progress, maxSpeed) {
        if (progress > 1) progress = 1;
        else if (progress < 0) progress = 0;
        if (progress < 0.33) {
            this.speedMask.rotation = 90;
            this.speedMask.x = 61;
            this.speedMask.y = progress / 0.33 * 72;
        }
        else if (progress < 0.66) {
            this.speedMask.rotation = 90 - (progress - 0.33) / 0.33 * 90;
            this.speedMask.x = Math.random() + 60;
            this.speedMask.y = Math.random() + 70;
        }
        else {
            this.speedMask.rotation = 0;
            this.speedMask.y = 66;
            this.speedMask.x = (progress - 0.66) / 0.34 * 77 + 61;
        }
        this.fcSpeed.value = (progress * maxSpeed).toFixed(0);
    }


