import Utility from "../Mod/Utility";
import SGAD from "../SGSDK/SGAD";
import SGMgr from "../SGSDK/SGMgr";
import Strength from "../Utils/Strength";

export default class StrengthUI extends Laya.Scene {
    closeBtn: Laya.Image;
    videoBtn: Laya.Image;

    onOpened() {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        Utility.addClickEvent(this.closeBtn, this, this.close, null, false);
        Utility.addClickEvent(this.videoBtn, this, this.videoClick);
    }

    onClosed(type?: string): void {
        Laya.timer.clearAll(this);
        SGMgr.inHome();
    }

    videoClick() {
        SGAD.showVideoAd(() => {
            Strength.Share.addStrength(1, 10);
            this.close();
        });
    }
}