import GameLogic from "../Crl/GameLogic"
import SoundMgr from "../Mod/SoundMgr"
import PlayerDataMgr from "../Libs/PlayerDataMgr"
import Utility from "../Mod/Utility"
import CommonData from "../Utils/CommonData"
import QuickTool3D from "../Utils/QuickTool3D"

export default class LoadingUI extends Laya.Scene {
    bar: Laya.Image
    imgBar: Laya.Image;

    onOpened() {
        this.bar.x = -315;
        Laya.timer.frameLoop(1, this, () => {
            this.bar.x += 1;
            if (this.bar.x > 0) this.bar.x = 0;
        });

        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        this.loadJsonData(1)
        
        Utility.loadJson('res/configs/carConfig.json', (data)=>{
            PlayerDataMgr.carConfig = data;
        })
    }

    onClosed(type?: string): void {
        Laya.timer.clearAll(this);
        QuickTool3D.clearMask(this.imgBar);
    }

    loadJsonData(index: number) {
        var next = () => {
            if (Laya.Browser.onWeiXin)
                this.loadSubpackage()
            else {
                GameLogic.Share.loadRoads();
                this.loadRes()
            }
        }

        if (CommonData.IsLoadJSON) {    //加载JSON
            Utility.loadJson('res/configs/Level' + index + '.json', (data) => {
                PlayerDataMgr.levelDataArr.push(data)
                index++
                if (index > CommonData.LevelCount) {
                    next();
                    console.log('levelDataArr:', PlayerDataMgr.levelDataArr)
                    return
                } else {
                    this.loadJsonData(index)
                }
            })
        }
        else { next(); }
    }

    loadSubpackage() {
        const loadTask = Laya.Browser.window.wx.loadSubpackage({
            name: 'unity', // name 可以填 name 或者 root
            success: (res) => {
                // 分包加载成功后通过 success 回调
                this.loadRes()
            },
            fail: (res) => {
                // 分包加载失败通过 fail 回调
                this.loadSubpackage()
            }
        })

        loadTask.onProgressUpdate(res => {
            console.log('下载进度', res.progress)
            console.log('已经下载的数据长度', res.totalBytesWritten)
            console.log('预期需要下载的数据总长度', res.totalBytesExpectedToWrite)
        })

        Laya.Browser.window.wx.loadSubpackage({
            name: 'pack', // name 可以填 name 或者 root
            success: (res) => {
                // 分包加载成功后通过 success 回调
                GameLogic.Share.loadRoads();
            },
            fail: (res) => {
            }
        })
        Laya.Browser.window.wx.loadSubpackage({
            name: 'sound', // name 可以填 name 或者 root
            success: (res) => {
                // 分包加载成功后通过 success 回调
                // GameLogic.Share.loadRoads();
            },
            fail: (res) => {
            }
        })
    }

    loadRes() {
        // //预加载3d资源
        // var resUrl = [
        //     WxApi.UnityPath + 'Player.lh',
        //     WxApi.UnityPath + 'Enemy.lh',
        //     WxApi.UnityPath + 'Barrel.lh',
        //     WxApi.UnityPath + 'Wall.lh',
        //     WxApi.UnityPath + 'Road.lh',
        //     WxApi.UnityPath + 'Jumper.lh',
        //     WxApi.UnityPath + 'Mutagen.lh',
        //     WxApi.UnityPath + 'Finish.lh',
        //     WxApi.UnityPath + 'Lava_Pool.lh',
        //     WxApi.UnityPath + 'Points_plate.lh',
        //     WxApi.UnityPath + 'Road_Finish.lh',
        //     WxApi.UnityPath + 'Stand.lh',
        //     WxApi.UnityPath + 'SelectNodeL.lh',
        //     WxApi.UnityPath + 'SelectNodeR.lh',
        //     WxApi.UnityPath + 'DropArea.lh',
        //     WxApi.UnityPath + 'Boss.lh',
        //     WxApi.UnityPath + 'Wormhole.lh',
        //     WxApi.UnityPath + 'WaveGun.lh',
        //     WxApi.UnityPath + 'FX_Green.lh',
        //     WxApi.UnityPath + 'FX_Hit_1.lh',
        //     WxApi.UnityPath + 'lightning_1.lh'
        // ];
        // Laya.loader.create(resUrl, Laya.Handler.create(this, this.onComplete));

        Laya.timer.once(500, this, this.onComplete);
        // this.onComplete();
    }

    onComplete() {
        SoundMgr.instance.initLoading(() => {
            this.bar.x = 0;
            Laya.timer.once(100, this, () => {
                GameLogic.Share.initScene();
            });
        })
    }
}