export default class Strength {
    static get Share(): Strength {
        if (!this.share) {
            this.share = new Strength;
        }
        return this.share;
    }
    static share: Strength;

    /**当前体力 */
    currentStrength: number;
    /**体力上限值 */
    maxStrength: number;
    /**恢复间隔-分钟 */
    recoverTime: number;
    /**单局消耗 */
    costStrength: number;
    /**恢复点数-视频 */
    recoverValue_Video: number;
    /**恢复点数-分享 */
    recoverValue_Share: number;

    /**是否通过观看视频获取体力 */
    isGetStrengthByVideo: boolean

    countTime: number;

    strength: number;
    offlineTime: number;

    init() {
        this.costStrength = 1;
        this.maxStrength = 10;
        this.recoverTime = 5;
        this.recoverValue_Share = 10;
        this.recoverValue_Video = 10;

        let strength = Laya.LocalStorage.getItem("strength");
        if (strength) {
            this.strength = JSON.parse(strength);
            this.currentStrength = this.strength;
            this.offlineTime = JSON.parse(Laya.LocalStorage.getItem("offlineTime"));
        }
        else {
            this.strength = this.maxStrength;
            this.currentStrength = this.strength;
            this.offlineTime = new Date().getTime();
            this.updateData();
        }

        //离线体力
        let curTime = new Date().getTime() - this.offlineTime;
        let minutes = curTime / 60000;
        let newStrength = Math.floor(minutes / this.recoverTime);
        if (newStrength >= this.maxStrength - this.currentStrength) {
            newStrength = this.maxStrength - this.currentStrength;
        }
        this.addStrength(3, newStrength);

        if (this.currentStrength < this.maxStrength) {
            //记录时间戳，实时计时，显示时间
            this.countTime = 0;
            Laya.timer.loop(1000, this, this.updateStrengthValue);
        }
    }

    /**
     * 增加体力
     * @param type 恢复类型：0-时间 1-视频 2-分享 3-其他
     */
    addStrength(type, value?) {
        switch (type) {
            case 0:
                this.currentStrength += 1;
                break;
            case 1:
                this.currentStrength += this.recoverValue_Video;
                break;
            case 2:
                this.currentStrength += this.recoverValue_Share;
                break;
            case 3:
                this.currentStrength += value;
                break;
        }

        if (this.currentStrength > this.maxStrength) {
            this.currentStrength = this.maxStrength;
        }

        this.updateData();
    }

    /**
     * 消耗体力
     * @param cb 回调
     */
    subStrength(cb) {
        if (this.currentStrength < this.costStrength) {
            //TODON 提示体力不足
            var canPlay = false;
            this.isGetStrengthByVideo = !this.isGetStrengthByVideo;
        }
        else {
            canPlay = true;
            if (this.currentStrength == this.maxStrength) {
                Laya.timer.clear(this, this.updateStrengthValue);
                this.countTime = 0;
                Laya.timer.loop(1000, this, this.updateStrengthValue);
            }
            this.currentStrength -= this.costStrength;
        }

        this.updateData();
        cb(canPlay);
    }

    get getStrengtByMaxStrength(): string {
        return this.currentStrength + "/" + this.maxStrength;
    }

    time: string;
    updateStrengthValue() {
        this.countTime++;
        let seconds = this.recoverTime * 60 - this.countTime; //*60
        this.time = this.getTimeLeft2BySecond(seconds);
        if (seconds == 0) {
            this.addStrength(0);
            if (this.currentStrength < this.maxStrength) {
                this.countTime = 0;
            }
            else {
                Laya.timer.clear(this, this.updateStrengthValue);
            }
        }
    }

    updateData() {
        //TODON 保存本地
        Laya.LocalStorage.setItem("strength", JSON.stringify(this.currentStrength));
        Laya.LocalStorage.setItem("offlineTime", JSON.stringify(new Date().getTime()));
    }

    /**秒转分:秒 */
    public getTimeLeft2BySecond(s: number): string {
        let minutes = Math.round((s - 30) / 60) % 60;
        let seconds = s % 60;

        var minStr = minutes > 9 ? minutes + ":" : "0" + minutes + ":";
        var secStr = seconds > 9 ? seconds + "" : "0" + seconds;
        return (minutes > 0 ? minStr : "00:") + (seconds > 0 ? (secStr + "") : "00");
    }
}