import GameLogic from "../Crl/GameLogic";
import PlayerDataMgr from "../Libs/PlayerDataMgr";
import Utility from "../Mod/Utility"
import SGAD from "../SGSDK/SGAD";
import CommonData from "../Utils/CommonData";
import Strength from "../Utils/Strength";

export default class SkinUI extends Laya.Scene {
    closeBtn: Laya.Image
    leftBtn: Laya.Image;
    rightBtn: Laya.Image;
    chooseId: number = 0

    upgradeTab: Laya.Image;
    wheelTab: Laya.Image;
    tailTab: Laya.Image;
    colorTab: Laya.Image

    imgCtrl: Laya.Image;
    imgSpeed: Laya.Image;
    imgAcce: Laya.Image;

    imgLock: Laya.Image;
    imgUnlock: Laya.Image;

    skinList: Laya.List;

    curTab = Tabs.upgradeTab;

    imgCarTitle: Laya.Image;
    lbCarName: Laya.Label;
    lbGrade: Laya.Label;

    imgNextCtrl: Laya.Image;
    imgNextSpd: Laya.Image;
    imgNextAcce: Laya.Image;

    imgCurCtrl: Laya.Image;
    imgCurSpd: Laya.Image;
    imgCurAcce: Laya.Image;

    lbCtrl: Laya.Label;
    lbSpd: Laya.Label;
    lbAcce: Laya.Label;

    lbCost: Laya.Label;

    upgradeBtn: Laya.Image;
    videoUpgradeBtn: Laya.Image;

    maxValue: number = 3;

    wheelIds: number[];
    imgDriver: Laya.Image;
    lbName:Laya.Label;

    unlockBtn:Laya.Image;

    onAwake(): void {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
    }

    onOpened() {
        Utility.addClickEvent(this.closeBtn, this, this.closeBtnCB)

        Utility.addClickEvent(this.upgradeTab, this, this.changeTab, [Tabs.upgradeTab]);
        Utility.addClickEvent(this.wheelTab, this, this.changeTab, [Tabs.wheelTab]);
        Utility.addClickEvent(this.tailTab, this, this.changeTab, [Tabs.tailTab]);
        Utility.addClickEvent(this.colorTab, this, this.changeTab, [Tabs.colorTab]);

        Utility.addClickEvent(this.leftBtn, this, this.changeCar, [false], false);
        Utility.addClickEvent(this.rightBtn, this, this.changeCar, [true], false);

        Utility.addClickEvent(this.upgradeBtn, this, this.upgradeClick);
        Utility.addClickEvent(this.videoUpgradeBtn, this, this.videoUpgradeClick);

        Utility.addClickEvent(this.unlockBtn, this, ()=>{
            Laya.Scene.open("MyScenes/ExpUI.scene", false, { carId: this.chooseId, isSkin: true });
        });

        this.initList();

        this.chooseId = PlayerDataMgr.getPlayerData().skinId;
        this.changeTab([Tabs.upgradeTab]);
        this.selectCar();

        GameLogic.Share.playerAnim.play("CameraTweenToShop");

        Laya.timer.frameLoop(1, this, this.myUpdate)

        
        if(CommonData.tryDriverId != 0){
            let data = PlayerDataMgr.driverUpgradeData[CommonData.tryDriverId- 1];
            this.imgDriver.skin = "driverUI/ksbs_img_tx" + CommonData.tryDriverId+ ".png";
            this.lbName.text = data.name + "(试用)";
        }
        else{
            let id = PlayerDataMgr.getPlayerData().driverId;
            if (id != 0) {
                this.imgDriver.skin = "driverUI/ksbs_img_tx" + id + ".png";
                this.lbName.text = PlayerDataMgr.driverUpgradeData[id - 1].name;
            }
        }
    }

    onClosed(type?: string): void {
        Laya.timer.clearAll(this);
    }

    upgradeClick() {
        let cost = Number(this.lbCost.text);
        if (PlayerDataMgr.getPlayerData().coin >= cost) {
            PlayerDataMgr.changeCoin(-cost);
            this.updateCar();
        }
    }

    updateCar() {
        let carUpgradeData = PlayerDataMgr.carUpgradeData[this.chooseId];
        let carLvl = PlayerDataMgr.getPlayerData().skinLvl[this.chooseId];

        let anim = this["Upgrade"] as Laya.FrameAnimation;
        anim.play(0, false);

        Laya.timer.once(166.7, this, () => {
            this.updateCtrlValue(carUpgradeData, carLvl + 1);
        });

        Laya.timer.once(250.05, this, () => {
            this.updateSpdValue(carUpgradeData, carLvl + 1);
        });

        Laya.timer.once(333.4, this, () => {
            this.updateAcceValue(carUpgradeData, carLvl + 1);
        });

        PlayerDataMgr.getPlayerData().skinLvl[this.chooseId] += 1;
        PlayerDataMgr.setPlayerData();

        let cost = carUpgradeData.upgradeCost + (carLvl - 1) * carUpgradeData.upgradeCostStep;
        this.lbCost.text = cost.toString();

        let data = PlayerDataMgr.carConfig[this.chooseId];
        this.lbCarName.text = data.CarName + " Lv" + PlayerDataMgr.getPlayerData().skinLvl[this.chooseId];

        this.showFX();
    }

    showFX() {
        if (GameLogic.Share.upgradeFX.active) {
            GameLogic.Share.upgradeFX.active = false;
        }
        GameLogic.Share.upgradeFX.active = true;
        Laya.timer.clear(this, this.hideFX);
        Laya.timer.once(1000, this, this.hideFX);
    }

    hideFX() {
        GameLogic.Share.upgradeFX.active = false;
    }

    videoUpgradeClick() {
        SGAD.showVideoAd(() => {
            for (let i = 0; i < 5; i++) {
                let time = 420 * i
                Laya.timer.once(time, this, () => {
                    this.updateCar();
                });
            }
        });
    }

    changeCar(args) {
        this.chooseId += (args[0] ? 1 : -1);
        this.selectCar();
    }

    selectCar() {
        if (this.chooseId < 0) this.chooseId = 9;
        else if (this.chooseId > 9) this.chooseId = 0;

        this.skinList.selectedIndex = -1;

        this.imgLock.visible = PlayerDataMgr.getPlayerData().skinArr[this.chooseId] == 0;
        this.imgUnlock.visible = this.curTab == Tabs.upgradeTab && !this.imgLock.visible;
        this.skinList.visible = this.curTab != Tabs.upgradeTab && !this.imgLock.visible;
        // console.log(this.chooseId)

        let data = PlayerDataMgr.carConfig[this.chooseId];
        this.lbCarName.text = data.CarName + " Lv" + PlayerDataMgr.getPlayerData().skinLvl[this.chooseId];
        this.lbGrade.text = data.grade;
        this.imgCarTitle.skin = "startUI/ksbs_dk_clsx_" + data.level + ".png"

        GameLogic.Share.updateSkin(this.chooseId);

        if (this.curTab == Tabs.wheelTab) {
            this.updateWheelData();
        }
        else if (this.curTab == Tabs.tailTab) {
            this.updateTailData();
        }
        else if (this.curTab == Tabs.colorTab) {
            this.updateColorData();
        }
        else {
            this.updateUpgradeData();
        }
    }

    updateUpgradeData() {
        let carUpgradeData = PlayerDataMgr.carUpgradeData[this.chooseId];
        let carLvl = PlayerDataMgr.getPlayerData().skinLvl[this.chooseId];

        this.updateCtrlValue(carUpgradeData, carLvl);
        this.updateSpdValue(carUpgradeData, carLvl);
        this.updateAcceValue(carUpgradeData, carLvl);

        let cost = carUpgradeData.upgradeCost + (carLvl - 1) * carUpgradeData.upgradeCostStep;
        this.lbCost.text = cost.toString();
    }

    updateCtrlValue(carUpgradeData, carLvl) {
        let ctrl = this.getUpgradeData(carUpgradeData.ctrl, carUpgradeData.addCtrl, carLvl);
        let nextCtrl = this.getUpgradeData(carUpgradeData.ctrl, carUpgradeData.addCtrl, carLvl + 1);
        this.imgCurCtrl.width = 180 * (ctrl / this.maxValue);
        this.imgNextCtrl.width = 180 * (nextCtrl / this.maxValue);
        this.lbCtrl.text = ctrl.toFixed(2);
    }

    updateSpdValue(carUpgradeData, carLvl) {
        let spd = this.getUpgradeData(carUpgradeData.spd, carUpgradeData.addSpd, carLvl);
        let nextSpd = this.getUpgradeData(carUpgradeData.spd, carUpgradeData.addSpd, carLvl + 1);
        this.imgCurSpd.width = 180 * (spd / this.maxValue);
        this.imgNextSpd.width = 180 * (nextSpd / this.maxValue);
        this.lbSpd.text = spd.toFixed(2);
    }

    updateAcceValue(carUpgradeData, carLvl) {
        let acce = this.getUpgradeData(carUpgradeData.acce, carUpgradeData.addAcce, carLvl);
        let nextAcce = this.getUpgradeData(carUpgradeData.acce, carUpgradeData.addAcce, carLvl + 1);
        this.imgCurAcce.width = 180 * (acce / this.maxValue);
        this.imgNextAcce.width = 180 * (nextAcce / this.maxValue);
        this.lbAcce.text = acce.toFixed(2);
    }

    getUpgradeData(firstValue, stepValue, level) {
        if (level == 1) {
            return firstValue;
        }
        else {
            return this.getUpgradeData(firstValue, stepValue, level - 1) + stepValue;
        }
    }

    changeTab(tab) {
        this.skinList.selectedIndex = -1;

        for (let tabItem in Tabs) {
            let str: string[] = this[tabItem].skin.split('.');
            let newStr = str[0].slice(0, str[0].length - 1);
            this[tabItem].skin = tabItem == tab[0] ? newStr + "2.png" : newStr + "1.png";
        }

        let isUpgrade = false;
        switch (tab[0]) {
            case Tabs.upgradeTab:
                isUpgrade = true
                break;
            case Tabs.wheelTab:
                this.skinList.repeatX = 6;
                this.updateWheelData();
                break;
            case Tabs.tailTab:
                this.skinList.repeatX = 11;
                this.updateTailData();
                break;
            case Tabs.colorTab:
                this.skinList.repeatX = 10;
                this.updateColorData();
                break;
        }

        this.imgCtrl.visible = isUpgrade;
        this.imgSpeed.visible = isUpgrade;
        this.imgAcce.visible = isUpgrade;
        this.imgLock.visible = PlayerDataMgr.getPlayerData().skinArr[this.chooseId] == 0;
        this.skinList.visible = !isUpgrade && !this.imgLock.visible;
        this.imgUnlock.visible = isUpgrade && !this.imgLock.visible;

        if (this.curTab != Tabs.tailTab && tab == Tabs.tailTab) {
            GameLogic.Share.playerAnim.play("CameraTweenToTail");
        }
        else if (this.curTab == Tabs.tailTab && tab != Tabs.tailTab) {
            GameLogic.Share.playerAnim.play("CameraTweenToWheel");
        }
        this.curTab = tab;
    }

    closeBtnCB() {
        this["HideUI"].play();
        GameLogic.Share.playerAnim.play("CameraTweenToHome");
        Laya.timer.once(500, this, () => {
            Laya.Scene.open('MyScenes/StartUI.scene')
        });
    }

    initList() {
        this.skinList.renderHandler = new Laya.Handler(this, (cell, index) => {
            this.updateCellItem(cell, index);
        }, null, false);
        this.skinList.selectHandler = new Laya.Handler(this, (index) => {
            if (index == -1) return;
            let item = this.skinList.array[index];

            if (item.isLock) {
                SGAD.showVideoAd(() => {
                    if (this.curTab == Tabs.wheelTab) {
                        let wheelId = this.wheelIds[index];
                        this.changeWheel(wheelId, true);
                    }
                    else if (this.curTab == Tabs.tailTab) {
                        this.changeTail(index, true);
                    }
                    else if (this.curTab == Tabs.colorTab) {
                        this.changeColor(index + 1, true);
                    }
                });
            }
            else {
                if (this.curTab == Tabs.wheelTab) {
                    let wheelId = this.wheelIds[index];
                    this.changeWheel(wheelId);
                }
                else if (this.curTab == Tabs.tailTab) {
                    this.changeTail(index);
                }
                else if (this.curTab == Tabs.colorTab) {
                    this.changeColor(index + 1);
                }
            }
        }, null, false);
        this.skinList.selectEnable = true;
        this.skinList.elasticEnabled = true;
        this.skinList.hScrollBarSkin = "";
    }

    updateCellItem(cell, index) {
        let bg = cell;
        let icon = cell.getChildAt(0);
        let colorPl = cell.getChildAt(1);
        let colorFg = colorPl.getChildAt(0);
        let imgLock = cell.getChildAt(2);

        let item = this.skinList.array[index];
        bg.bottom = 0;
        if (item.isLock) {
            bg.skin = "skinUI/clgz_btn_xzk_1.png";
            colorFg.skin = "skinUI/clgz_btn_clts_1.png";
            imgLock.skin = "skinUI/tab_sp_1.png";
        }
        else {
            bg.skin = "skinUI/clgz_btn_xzk_2.png";
            colorFg.skin = "skinUI/clgz_btn_clts_2.png";
            if (item.isSelect) {
                imgLock.skin = "skinUI/tab_sp_2.png";
                bg.bottom = 10;
            }
            else {
                // bg.skin = "skinUI/clgz_btn_xzk_1.png";
                imgLock.skin = "skinUI/tab_yjs_1.png";
            }
        }

        colorPl.visible = item.isColor;
        if (!item.isColor) {
            icon.skin = item.iconSkin;
        }
        else {
            colorPl.bgColor = item.color;
        }
    }

    changeWheel(wheelId, isAdd = false) {
        if (isAdd) {
            PlayerDataMgr.getPlayerData().skinWheel[this.chooseId].push(wheelId);
        }
        PlayerDataMgr.getPlayerData().wheelId[this.chooseId] = wheelId;
        PlayerDataMgr.setPlayerData();
        GameLogic.Share.createWheel(GameLogic.Share.showCar, wheelId);
        this.updateWheelData();
        // let cell = this.skinList.cells[wheelId - 1];
        // this.updateCellItem(cell, wheelId - 1);
    }

    changeTail(tailId, isAdd = false) {
        if (isAdd) {
            PlayerDataMgr.getPlayerData().skinTail[this.chooseId].push(tailId);
        }
        PlayerDataMgr.getPlayerData().tailId[this.chooseId] = tailId;
        PlayerDataMgr.setPlayerData();
        GameLogic.Share.createTail(GameLogic.Share.showCar, tailId);
        this.updateTailData();
        // let cell = this.skinList.cells[tailId];
        // this.updateCellItem(cell, tailId);
    }

    changeColor(colorId, isAdd = false) {
        if (isAdd) {
            PlayerDataMgr.getPlayerData().skinColor[this.chooseId].push(colorId);
        }
        PlayerDataMgr.getPlayerData().colorId[this.chooseId] = colorId;
        PlayerDataMgr.setPlayerData();
        GameLogic.Share.createColor(GameLogic.Share.showCar, this.chooseId, colorId);
        this.updateColorData();
        // let item = this.skinList.array[colorId - 1];
        // let cell = this.skinList.cells[colorId - 1];
        // this.updateCellItem(cell, colorId - 1);
    }

    updateWheelData() {
        let data = [];
        let id = this.chooseId;

        switch (id) {
            case 0: var wheelIds = [1, 2, 3, 4, 5, 6]; break;
            case 1: wheelIds = [2, 3, 4, 5, 6, 1]; break;
            case 2: wheelIds = [3, 4, 5, 6, 1, 2]; break;
            case 3: wheelIds = [4, 5, 6, 1, 2, 3]; break;
            case 4: wheelIds = [5, 6, 1, 2, 3, 4]; break;
            case 5: wheelIds = [6, 1, 2, 3, 4, 5]; break;
            case 6: wheelIds = [1, 2, 3, 4, 5, 6]; break;
            case 7: wheelIds = [2, 3, 4, 5, 6, 1]; break;
            case 8: wheelIds = [3, 4, 5, 6, 1, 2]; break;
            case 9: wheelIds = [4, 5, 6, 1, 2, 3]; break;
        }
        this.wheelIds = wheelIds;
        for (let i = 0; i < 6; i++) {
            let item = {
                isColor: false,
                isSelect: PlayerDataMgr.getPlayerData().wheelId[id] == wheelIds[i],
                isLock: PlayerDataMgr.getPlayerData().skinWheel[id].indexOf(wheelIds[i]) == -1,
                iconSkin: "skinUI/icon_wheel_" + wheelIds[i] + ".png"
            }
            data.push(item);
        }
        this.skinList.array = data;
    }

    updateTailData() {
        let data = [];
        let id = this.chooseId;

        for (let i = 0; i < 11; i++) {
            let item = {
                isColor: false,
                isSelect: PlayerDataMgr.getPlayerData().tailId[id] == i,
                isLock: PlayerDataMgr.getPlayerData().skinTail[id].indexOf(i) == -1,
                iconSkin: i == 0 ? "" : "skinUI/icon_tail_" + i + ".png"
            }
            data.push(item);
        }
        this.skinList.array = data;
    }

    updateColorData() {
        let data = [];
        let id = this.chooseId;

        for (let i = 0; i < 10; i++) {
            let item = {
                isColor: true,
                isSelect: PlayerDataMgr.getPlayerData().colorId[id] == (i + 1),
                isLock: PlayerDataMgr.getPlayerData().skinColor[id].indexOf(i + 1) == -1,
                color: PlayerDataMgr.carConfig[id]["CarColor" + (i + 1)]
            }
            data.push(item);
        }
        this.skinList.array = data;
    }

    coinNum: Laya.Label;

    lbStrength: Laya.Label;
    lbStrengthTime: Laya.Label;
    myUpdate() {
        this.coinNum.text = PlayerDataMgr.getPlayerData().coin.toString();

        this.lbStrengthTime.visible = Strength.Share.currentStrength < Strength.Share.maxStrength;
        this.lbStrength.text = Strength.Share.currentStrength + "/" + Strength.Share.maxStrength;
        if (this.lbStrengthTime.visible && Strength.Share.time) {
            this.lbStrengthTime.text = Strength.Share.time;
        }
    }
}

enum Tabs {
    upgradeTab = "upgradeTab",
    wheelTab = "wheelTab",
    tailTab = "tailTab",
    colorTab = "colorTab"
}