import PlayerDataMgr from "../Libs/PlayerDataMgr";
import SoundMgr from "../Mod/SoundMgr";
import Utility from "../Mod/Utility";

export default class OptionUI extends Laya.Scene {
    closeBtn:Laya.Image;
    musicBtn:Laya.Image;
    soundBtn:Laya.Image;
    vibrateBtn:Laya.Image;

    onOpened() {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        Utility.addClickEvent(this.closeBtn, this, this.close, null,  false);
        Utility.addClickEvent(this.musicBtn, this, this.musicClick);
        Utility.addClickEvent(this.soundBtn, this, this.soundClick);
        Utility.addClickEvent(this.vibrateBtn, this, this.vibrateClick);

        this.updateData();
    }

    onClosed(type?: string): void {
        Laya.timer.clearAll(this);
    }

    updateData(){
        let options = PlayerDataMgr.getPlayerData().option;
        this.musicBtn.skin = options[0] ? "commonUI/lxsy_btn_k.png":"commonUI/lxsy_btn_g.png";
        this.soundBtn.skin = options[1] ? "commonUI/lxsy_btn_k.png":"commonUI/lxsy_btn_g.png";
        this.vibrateBtn.skin = options[2] ? "commonUI/lxsy_btn_k.png":"commonUI/lxsy_btn_g.png";
    }

    musicClick(){
        let options = PlayerDataMgr.getPlayerData().option
        options[0] = options[0] == 1 ? 0 : 1;
        PlayerDataMgr.setPlayerData();
        this.musicBtn.skin = options[0] ? "commonUI/lxsy_btn_k.png":"commonUI/lxsy_btn_g.png";

        if(options[0]){
            SoundMgr.instance.playMusic("bgm");
        }
        else{
            SoundMgr.instance.stopMusic();
        }
    }

    soundClick(){
        let options = PlayerDataMgr.getPlayerData().option
        options[1] = options[1] == 1 ? 0 : 1;
        PlayerDataMgr.setPlayerData();
        this.soundBtn.skin = options[1] ? "commonUI/lxsy_btn_k.png":"commonUI/lxsy_btn_g.png";
    }

    vibrateClick(){
        let options = PlayerDataMgr.getPlayerData().option
        options[2] = options[2] == 1 ? 0 : 1;
        PlayerDataMgr.setPlayerData();
        this.vibrateBtn.skin = options[2] ? "commonUI/lxsy_btn_k.png":"commonUI/lxsy_btn_g.png";
    }
}