import GameLogic from "./GameLogic";
import TraceProgressTracker from "./TraceProgressTracker";

export default class HurdleComp extends TraceProgressTracker {
    static hurdles: HurdleComp[];

    hurdle: Laya.Sprite3D;
    lPosX: number;

    onStart(): void {
        if (!HurdleComp.hurdles) { HurdleComp.hurdles = []; }
        HurdleComp.hurdles.push(this);
        this.hurdle = this.self.getChildAt(0) as Laya.Sprite3D;
        let pos = this.hurdle.transform.localPosition.clone();
        pos.x = this.lPosX;
        this.hurdle.transform.localPosition = pos;
    }

    init(minDistance?: number, randomDistance?: number): void {
        super.init(minDistance, randomDistance);
    }

    setPos(minDistance = 0, randomDistance = 0, lPosX = 0) {
        this.init(minDistance, randomDistance);
        this.lPosX = lPosX;
    }

    onUpdate(): void {
        let cars = GameLogic.Share.carComps;
        for (let i = 0; i < cars.length; i++) {
            if (Math.abs(cars[i].progressDistance - this.progressDistance) < 3) {
                if (Math.abs(cars[i].role.transform.localPosition.x - this.hurdle.transform.localPosition.x) < 3.9) {
                    // console.log("撞到了:");
                    cars[i].boom();
                }
            }
        }
    }
}