import MyVector3 from "../Utils/MyVector3";
import GameLogic from "./GameLogic";
import TraceProgressTracker from "./TraceProgressTracker";

export default class HammerComp extends TraceProgressTracker {
    static hammers: HammerComp[];
    isTrigger: boolean = false;

    hammer: Laya.Sprite3D;
    lPosX: number;

    onStart(): void {
        if (!HammerComp.hammers) { HammerComp.hammers = []; }
        HammerComp.hammers.push(this);

        this.hammer = this.self.getChildAt(0).getChildAt(0) as Laya.Sprite3D;
        let pos = this.hammer.transform.localPosition.clone();
        pos.x = this.lPosX;
        this.hammer.transform.localPosition = pos;
    }

    init(minDistance?: number, randomDistance?: number): void {
        super.init(minDistance, randomDistance);
    }

    setPos(minDistance = 0, randomDistance = 0, lPosX = 0) {
        this.init(minDistance, randomDistance);
        this.lPosX = lPosX
    }

    triggerStart() {
        if (this.isTrigger) return;
        this.isTrigger = true;
    }

    onUpdate(): void {
        this.hammer.transform.rotate(new Laya.Vector3(-0.03, 0, 0));

        let cars = GameLogic.Share.carComps;
        for (let i = 0; i < cars.length; i++) {
            if (Math.abs(cars[i].progressDistance - this.progressDistance) < 4) {
                (this.hammer as any)._children.forEach(element => {
                    let dis = MyVector3.distance(element.transform.position.clone(), cars[i].role.transform.position);
                    // console.log(dis)
                    if (Math.abs(this.hammer.transform.localPosition.x - cars[i].role.transform.localPosition.x) < 2.2 && dis < 2) {
                        // console.log("撞到了:", element.name);
                        cars[i].boom();
                    }
                });
            }
        }
    }
}