import GameLogic from "./GameLogic";
import TraceProgressTracker from "./TraceProgressTracker";

export default class TortureComp extends TraceProgressTracker {
    static tortures: TortureComp[];

    torture: Laya.Sprite3D;
    lPosX: number;

    isLeft: boolean = true;

    onStart(): void {
        if (!TortureComp.tortures) { TortureComp.tortures = []; }
        TortureComp.tortures.push(this);
        this.torture = this.self.getChildAt(0) as Laya.Sprite3D;
        let pos = this.torture.transform.localPosition.clone();
        pos.x = this.lPosX;
        this.torture.transform.localPosition = pos;
    }

    init(minDistance?: number, randomDistance?: number): void {
        super.init(minDistance, randomDistance);
    }

    setPos(minDistance = 0, randomDistance = 0, lPosX = 0) {
        this.init(minDistance, randomDistance);
        this.lPosX = lPosX;
    }

    onUpdate(): void {
        let pos = this.torture.transform.localPosition.clone();
        if (this.isLeft) {
            pos.x += 0.05;
            if(pos.x > this.maxPosX){
                pos.x = this.maxPosX;
                this.isLeft = false;
            }
        }
        else{
            pos.x -= 0.05;
            if(pos.x < -this.maxPosX){
                pos.x = -this.maxPosX;
                this.isLeft = true;
            }
        }
        this.torture.transform.localPosition = pos;
        this.recalcPos();
        this.torture.transform.rotate(new Laya.Vector3(0, 0.1, 0));

        let cars = GameLogic.Share.carComps;
        for (let i = 0; i < cars.length; i++) {
            if (Math.abs(cars[i].progressDistance - this.progressDistance) < 3) {
                if (Math.abs(cars[i].role.transform.localPosition.x - this.torture.transform.localPosition.x) < 2) {
                    // console.log("撞到了:");
                    cars[i].boom();
                }
            }
        }
    }
}