import PlayerDataMgr from "../Libs/PlayerDataMgr";
import WxApi from "../Libs/WxApi";
import SoundMgr from "../Mod/SoundMgr";
import GameUI from "../View/GameUI";
import GameLogic from "./GameLogic";
import TraceProgressTracker from "./TraceProgressTracker";

export default class ChestComp extends TraceProgressTracker {
    static items: ChestComp[];
    child: Laya.Sprite3D;

    isTrigger: boolean = false;

    onStart(): void {
        if (!ChestComp.items) { ChestComp.items = []; }
        ChestComp.items.push(this);
    }

    onDestroy(): void {
        super.onDestroy();
        let index = ChestComp.items.indexOf(this);
        if (index != -1) {
            ChestComp.items.splice(index, 1);
        }
    }

    width: number = 2;
    length: number = 2;
    setSize(width, length) {
        this.width = width;
        this.length = length;
    }

    init(minDistance?: number, randomDistance?: number): void {
        super.init(minDistance, randomDistance);
    }

    setPos(minDistance = 0, randomDistance = 0, lPosX = 0) {
        this.init(minDistance, randomDistance);
        let child = this.self.getChildAt(0) as Laya.Sprite3D
        this.child = child;
        let lPos = child.transform.localPosition.clone();
        lPos.x = lPosX;
        child.transform.localPosition = lPos;
        this.recalcPos();
    }

    onUpdate(): void {
        if (this.self.destroyed) return;
        if (this.isTrigger) return;

        let car = GameLogic.Share.playerComp;
        if (Math.abs(car.progressDistance - this.progressDistance) < this.length) {
            if (Math.abs(car.role.transform.localPosition.x - this.child.transform.localPosition.x) < this.width - 0.2) {
                this.hit(car.roleChild);
            }
        }
    }

    hit(car) {
        if (this.isTrigger) return;
        this.isTrigger = true;
        WxApi.DoVibrate(true);
        let fx = GameLogic.Share.getFx("ChestFX", 2000);
        car.addChild(fx);
        fx.transform.localPosition = new Laya.Vector3(0, 0.2, 0);
        fx.transform.rotationEuler = new Laya.Vector3();
        fx.transform.setWorldLossyScale(new Laya.Vector3(2, 2, 2));
        this.self.destroy();
        PlayerDataMgr.getPlayerData().exp += 100;
        PlayerDataMgr.setPlayerData();
        SoundMgr.instance.playSoundEffect('money');

        GameUI.Share.pushGetTip(0);
    }
}