import GameLogic from "./GameLogic";
import TraceProgressTracker from "./TraceProgressTracker";

export default class TonnelComp extends TraceProgressTracker {
    static tonnels: TonnelComp[];

    tonnel: Laya.Sprite3D;
    type: number;

    onStart(): void {
        if (!TonnelComp.tonnels) { TonnelComp.tonnels = []; }
        TonnelComp.tonnels.push(this);

        this.tonnel = this.self.getChildAt(0) as Laya.Sprite3D;
        if (this.type == 1) {
            this.tonnel.transform.localRotationEuler = new Laya.Vector3(0, 0, 45);
        }
        else if (this.type == 2) {
            this.tonnel.transform.localRotationEuler = new Laya.Vector3(0, 0, -45);
        }
    }

    init(minDistance?: number, randomDistance?: number): void {
        super.init(minDistance, randomDistance);
    }

    setPos(minDistance = 0, randomDistance = 0, type = 0) {
        this.init(minDistance, randomDistance);
        this.type = type;
    }

    onUpdate(): void {
        if(this.type!=0){
            let cars = GameLogic.Share.carComps;
            for (let i = 0; i < cars.length; i++) {
                if (Math.abs(cars[i].progressDistance - this.progressDistance) < 2) {
                    if(this.type == 2){
                        if(cars[i].role.transform.localPosition.x > -2.8){
                            // console.log("撞到了:");
                            cars[i].boom();
                        }
                    }
                    else{
                        if(cars[i].role.transform.localPosition.x < 2){
                            // console.log("撞到了:");
                            cars[i].boom();
                        }
                    }
                }
            }
        }
    }
}