import GameLogic from "../Crl/GameLogic"
import PlayerDataMgr from "../Libs/PlayerDataMgr"
import Utility from "../Mod/Utility"
import SGAD from "../SGSDK/SGAD"
import SGMgr from "../SGSDK/SGMgr"
import CommonData from "../Utils/CommonData"

export default class FinishUI extends Laya.Scene {
    // constructor() {
    //     super()
    // }
    // coinNum: Laya.FontClip

    winTitle: Laya.Image
    // unlockNode: Laya.Image
    // bounesIcon: Laya.Image

    // nextBtn: Laya.Image

    // btnVideo: Laya.Image;
    // factor = 1;

    imgLight: Laya.Image;
    imgRank: Laya.Image;
    // freeSkin: any[] = []

    imgCondition: Laya.Image;
    lbReward: Laya.Label;
    lbExp: Laya.Label;

    upgradeBtn: Laya.Image;
    nextBtn: Laya.Image;
    turnBtn:Laya.Image;

    imgCarTitle: Laya.Image;
    lbCarName: Laya.Label;
    lbGrade: Laya.Label;

    carBtn: Laya.Image;
    imgCar: Laya.Image;
    unlockId: number = 1;
    imgExpProgress: Laya.Image;

    reward:number;
    exp:number;

    onOpened() {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)

        let chooseId = PlayerDataMgr.getPlayerData().skinId;
        let data = PlayerDataMgr.carConfig[chooseId];
        this.lbCarName.text = data.CarName + " Lv" + PlayerDataMgr.getPlayerData().skinLvl[chooseId];
        this.lbGrade.text = data.grade;
        this.imgCarTitle.skin = "startUI/ksbs_dk_clsx_" + data.level + ".png";

        // this.coinNum.value = PlayerDataMgr.getPlayerData().coin.toString()
        let isWin = GameLogic.Share.isWin
        this.winTitle.skin = isWin ? "finishUI/js_wz_tzcg.png" : "finishUI/js_wz_tzsb.png";
        this.imgCondition.skin = isWin ? "finishUI/js_wz_3.png" : "finishUI/js_wz_2.png";
        this.imgRank.skin = "finishUI/js_jb_" + GameLogic.Share.myRank + ".png"

        // // this.bounesIcon.visible = isWin
        // // this.nextBtn.visible = isWin
        this.upgradeBtn.visible = !isWin;

        this.reward = isWin ? 3000 : 1000;
        this.exp = isWin ? 400 : 200;
        PlayerDataMgr.getPlayerData().exp += this.exp;
        PlayerDataMgr.changeCoin(this.reward);

        Utility.addClickEvent(this.nextBtn, this, this.closeCB)
        Utility.addClickEvent(this.upgradeBtn, this, this.goToUpgrade)
        Utility.addClickEvent(this.turnBtn, this, this.goToDriver);

        Utility.addClickEvent(this.carBtn, this, ()=>{
            Laya.Scene.open("MyScenes/ExpUI.scene", false, { carId: this.unlockId, isSkin: true });
        });

        this.lbReward.text = "+" + this.reward;
        this.lbExp.text = "+" + this.exp;
        // // Utility.addClickEvent(this.restartBtn, this, this.closeCB)

        // Utility.addClickEvent(this.btnVideo, this, () => {
        //     SGAD.showVideoAd(() => {
        //         this.factor = 5;
        //         this.closeCB();
        //     });
        // })

        this.carBtn.visible = false;
        for (let i = 1; i < 10; i++) {
            if (PlayerDataMgr.getPlayerData().skinArr[i] == 0) {
                this.carBtn.visible = true;
                this.imgCar.skin = "startUI/Car_" + (i + 1) + ".png";
                this.unlockId = i;

                let myExp = PlayerDataMgr.getPlayerData().exp;
                let needExp = 1600 + (this.unlockId - 1) * 1600;
                this.imgExpProgress.width = 406 * Math.min(1, (myExp / needExp));
                break;
            }
        }

        if (isWin) {
            Laya.timer.frameLoop(1, this, () => {
                this.imgLight.rotation += 0.5;
            })
        }
        else {
            this.imgLight.visible = false;
        }


        SGMgr.inFinish()
    }

    onClosed() {
        Laya.timer.clearAll(this);
    }

    closeCB(openUpgrade = 0) {
        CommonData.tryDriverId = 0;
        SGMgr.backToHome(() => {
            if (GameLogic.Share.isWin) {
                PlayerDataMgr.getPlayerData().grade++
                PlayerDataMgr.setPlayerData()
            }
            GameLogic.Share.restartGame()
            Laya.Scene.open('MyScenes/StartUI.scene', true, { openUpgrade: openUpgrade });
        })
    }

    goToUpgrade() {
        this.closeCB(1);
    }

    goToDriver(){
        this.closeCB(2);
    }
}
