import PlayerDataMgr from "../Libs/PlayerDataMgr";
import WxApi from "../Libs/WxApi";
import Utility from "../Mod/Utility";
import SGAD from "../SGSDK/SGAD";
import SGMgr from "../SGSDK/SGMgr";
import CommonData from "../Utils/CommonData";
import D2Tween from "../Utils/D2Tween";
import Strength from "../Utils/Strength";

export default class DriverUI extends Laya.Scene {
    getBox: Laya.Box;

    tryBtn: Laya.Image;
    getBtn: Laya.Image;
    closeBtn: Laya.Image;
    closeGetBoxBtn: Laya.Image;
    videoBtn: Laya.Image;

    coinNum: Laya.Label
    lbStrength: Laya.Label;
    lbStrengthTime: Laya.Label;
    lbCtrl: Laya.Label;
    lbSpd: Laya.Label;
    lbAcce: Laya.Label;
    lbGrade: Laya.Label;
    lbPart: Laya.Label;
    lbGet: Laya.Label;

    imgGirl: Laya.Image;

    tab1: Laya.Image;
    tab2: Laya.Image;
    tab3: Laya.Image;

    imgGirlIcon: Laya.Image;
    imgName: Laya.Image;
    imgProgress: Laya.Image;
    imgStarBg: Laya.Image;
    imgFullStar: Laya.Image;
    imgProgressBg: Laya.Image;
    imgPart: Laya.Image;

    curTab: number = 1;
    imgDriver: Laya.Image;
    lbName: Laya.Label;

    onOpened() {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        Utility.addClickEvent(this.getBtn, this, this.showGetBox);
        Utility.addClickEvent(this.closeGetBoxBtn, this, this.hideGetBox);
        Utility.addClickEvent(this.videoBtn, this, this.videoClick);
        Utility.addClickEvent(this.closeBtn, this, this.closeCB, null, false);

        Utility.addClickEvent(this.tryBtn, this, () => {
            if (this.tryBtn.gray) {
                WxApi.OpenAlert("试用中");
                return;
            }
            SGAD.showVideoAd(() => {
                CommonData.tryDriverId = this.curTab;
                let data = PlayerDataMgr.driverUpgradeData[this.curTab - 1];
                this.imgDriver.skin = "driverUI/ksbs_img_tx" + this.curTab + ".png";
                this.lbName.text = data.name + "(试用)";
                this.tryBtn.gray = true;
            });
        });

        Laya.timer.frameLoop(1, this, this.myUpdate)
        this.getBoxTween();

        if (PlayerDataMgr.getPlayerData().driverId != 0) {
            this.curTab = PlayerDataMgr.getPlayerData().driverId;
        }
        this.changeTab(this.curTab);
        Utility.addClickEvent(this.tab1, this, this.clickTab1);
        Utility.addClickEvent(this.tab2, this, this.clickTab2);
        Utility.addClickEvent(this.tab3, this, this.clickTab3);
    }

    closeCB() {
        this["HideUI"].play();
        Laya.timer.once(600, this, () => {
            this.close();
        });
    }

    clickTab1() {
        this.tab1.skin = "driverUI/jsy_btn_jsy1_2.png";
        this.tab2.skin = "driverUI/jsy_btn_jsy2_1.png";
        this.tab3.skin = "driverUI/jsy_btn_jsy3_1.png";
        this.changeTab(1);
    }

    clickTab2() {
        this.tab1.skin = "driverUI/jsy_btn_jsy1_1.png";
        this.tab2.skin = "driverUI/jsy_btn_jsy2_2.png";
        this.tab3.skin = "driverUI/jsy_btn_jsy3_1.png";
        this.changeTab(2);
    }

    clickTab3() {
        this.tab1.skin = "driverUI/jsy_btn_jsy1_1.png";
        this.tab2.skin = "driverUI/jsy_btn_jsy2_1.png";
        this.tab3.skin = "driverUI/jsy_btn_jsy3_2.png";
        this.changeTab(3);
    }

    changeTab(tabIndex) {
        this.imgGirl.skin = "driverUI/jsy_img_jsy_" + tabIndex + ".png";
        this.imgName.skin = "driverUI/jsy_img_wz_" + tabIndex + ".png";
        this.imgGirlIcon.skin = "driverUI/icon_jsy_tx" + tabIndex + ".png";
        let img1 = this.getBox.getChildAt(3) as Laya.Image;
        let img2 = this.getBox.getChildAt(4) as Laya.Image;
        let img3 = this.getBox.getChildAt(5) as Laya.Image;
        img1.skin = this.imgGirl.skin;
        img2.skin = "driverUI/icon_jsy_" + tabIndex + ".png";
        img3.skin = "driverUI/icon_jsy_" + tabIndex + ".png";
        this.curTab = tabIndex;

        let data = PlayerDataMgr.driverUpgradeData[tabIndex - 1];
        let parts = PlayerDataMgr.getPlayerData().skinDriverParts[tabIndex - 1];
        let star = Math.floor(parts / 10);

        this.lbCtrl.text = "+" + (data.ctrl + (star > 0 ? data.addCtrl * (star - 1) : 0)).toFixed(2);
        this.lbSpd.text = "+" + (data.spd + (star > 0 ? data.addSpd * (star - 1) : 0)).toFixed(2);
        this.lbAcce.text = "+" + (data.acce + (star > 0 ? data.addAcce * (star - 1) : 0)).toFixed(2);
        this.lbGrade.text = "评分 " + data.grade;

        this.lbPart.text = (parts % 10) + "/10";
        this.imgProgress.width = 300 * ((parts % 10 / 10));

        for (let i = 0; i < 5; i++) {
            let starImg = this.imgStarBg.getChildAt(i) as Laya.Image;
            starImg.skin = star > i ? "driverUI/jsy_img_stars_2.png" : "driverUI/jsy_img_stars_1.png";
        }

        if (star > 0) {
            if (star >= 5) {
                this.imgFullStar.visible = true;
                this.lbPart.visible = false;
                this.imgProgressBg.visible = false;
                this.imgPart.visible = false;
                this.getBtn.gray = true;

                Laya.timer.once(1100, this, () => {
                    this.hideGetBox();
                });
            }
            else {
                this.imgFullStar.visible = false;
                this.lbPart.visible = true;
                this.imgProgressBg.visible = true;
                this.imgPart.visible = true;
                this.getBtn.gray = false;
            }

            PlayerDataMgr.getPlayerData().driverId = this.curTab;
            PlayerDataMgr.setPlayerData();
            //TODON 更新头像、名称

            this.imgDriver.skin = "driverUI/ksbs_img_tx" + this.curTab + ".png";
            this.lbName.text = data.name;
            this.tryBtn.visible = false;
        }
        else {
            this.imgFullStar.visible = false;
            this.lbPart.visible = true;
            this.imgProgressBg.visible = true;
            this.imgPart.visible = true;
            this.getBtn.gray = false;
            this.tryBtn.visible = true;

            if (CommonData.tryDriverId == tabIndex) {
                this.imgDriver.skin = "driverUI/ksbs_img_tx" + tabIndex + ".png";
                this.lbName.text = data.name + "(试用)";
                this.tryBtn.gray = true;
            }
            else {
                this.tryBtn.gray = false;
            }
        }
    }

    myUpdate() {
        this.coinNum.text = PlayerDataMgr.getPlayerData().coin.toString();

        this.lbStrengthTime.visible = Strength.Share.currentStrength < Strength.Share.maxStrength;
        this.lbStrength.text = Strength.Share.currentStrength + "/" + Strength.Share.maxStrength;
        if (this.lbStrengthTime.visible && Strength.Share.time) {
            this.lbStrengthTime.text = Strength.Share.time;
        }
    }

    onClosed(type?: string): void {
        Laya.timer.clearAll(this);
        SGMgr.inHome();
    }

    videoClick() {
        SGAD.showVideoAd(() => {
            let list = PlayerDataMgr.getPlayerData().rewardList[this.curTab - 1];
            let count = PlayerDataMgr.getPlayerData().watchVideoCount[this.curTab - 1];
            this.lbGet.text = "抽7次必得（" + (count + 1) + "/7）";

            if (count == 6 || (Math.random() > 0.85 && Math.random() < 0.15)) {
                //TODON 获得的动画之后 重置抽奖
                var pl = this.getBox.getChildAt(3).getChildAt(1) as Laya.Panel;
                WxApi.OpenAlert("碎片+10");
                console.log("碎片+10");
                PlayerDataMgr.getPlayerData().rewardList[this.curTab - 1] = [0, 0, 0, 0, 0, 0, 0];
                PlayerDataMgr.getPlayerData().watchVideoCount[this.curTab - 1] = 0;
                this.addPart(this.curTab, 10);
            }
            else {
                PlayerDataMgr.getPlayerData().watchVideoCount[this.curTab - 1] = count + 1;
                let ids = [];
                for (let i = 1; i < 7; i++) {
                    if (list[i] == 0) {
                        ids.push(i);
                    }
                }
                ids = ids.sort((a, b) => {
                    return Math.random() - 0.5;
                });

                list[ids[0]] = 1;
                pl = this.getBox.getChildAt(ids[0] + 3).getChildAt(1) as Laya.Panel;
                switch (ids[0]) {
                    case 1:
                        WxApi.OpenAlert("碎片+1");
                        console.log("碎片+1");
                        this.addPart(this.curTab, 1);
                        break;
                    case 2:
                        WxApi.OpenAlert("碎片+2");
                        console.log("碎片+2");
                        this.addPart(this.curTab, 1);
                        break;
                    case 3:
                        WxApi.OpenAlert("经验+300");
                        console.log("经验+300");
                        PlayerDataMgr.getPlayerData().exp += 300;
                        break;
                    case 4:
                        WxApi.OpenAlert("经验+100");
                        console.log("经验+100");
                        PlayerDataMgr.getPlayerData().exp += 100;
                        break;
                    case 5:
                        WxApi.OpenAlert("金钱+2000");
                        console.log("金钱+2000");
                        PlayerDataMgr.getPlayerData().coin += 2000;
                        break;
                    case 6:
                        WxApi.OpenAlert("金钱+5000");
                        console.log("金钱+5000");
                        PlayerDataMgr.getPlayerData().coin += 5000;
                        break;
                }
            }

            D2Tween.toScale(pl.parent as Laya.Image, 1, 1.2, 300, Laya.Ease.backIn, () => {
                D2Tween.toScale(pl.parent as Laya.Image, 1.2, 1, 300, Laya.Ease.backOut, () => {
                    D2Tween.toAlpha(pl, 0, 0.6, 500, null, () => {
                        this.updateRewardBox();
                    });
                });
            })
            PlayerDataMgr.setPlayerData();
        });
    }

    addPart(tabIndex, amount) {
        PlayerDataMgr.getPlayerData().skinDriverParts[tabIndex - 1] += amount;
        this.changeTab(tabIndex);
    }

    showGetBox() {
        if (this.getBtn.gray) return;
        this.updateRewardBox();
        this.getBox.visible = true;
    }

    hideGetBox() {
        this.getBox.visible = false;
    }

    getBoxTween() {
        // let 
        for (let i = 3; i < 10; i++) {
            let img = this.getBox.getChildAt(i) as Laya.Image;
            D2Tween.toPosition(img, { y: img.y + (Math.random() > 0.5 ? 10 : -10) }, 2000, null, null, true);
        }
    }

    updateRewardBox() {
        let watchVideoCount = PlayerDataMgr.getPlayerData().watchVideoCount[this.curTab - 1];
        this.lbGet.text = "抽7次必得（" + watchVideoCount + "/7）";
        for (let i = 3; i < 10; i++) {
            let img = this.getBox.getChildAt(i) as Laya.Image;
            (img.getChildAt(1) as Laya.Panel).alpha = PlayerDataMgr.getPlayerData().rewardList[this.curTab - 1][i - 3] == 1 ? 0.6 : 0;
        }
    }
}