import GameLogic from "./GameLogic";
import TraceProgressTracker from "./TraceProgressTracker";

export default class JumpComp extends TraceProgressTracker {
    static jumpComp: JumpComp[];
    // isTrigger: boolean = false;

    jump: Laya.Sprite3D;
    jumpIndex: number;

    onStart(): void {
        if (!JumpComp.jumpComp) { JumpComp.jumpComp = []; }
        JumpComp.jumpComp.push(this);

        // this.jump = this.self.getChildAt(0) as Laya.Sprite3D;
        // if (this.jumpIndex) {
        //     this.jump.transform.localPosition = new Laya.Vector3(this.jumpIndex, 0, 0);
        // }
        // this.recalcPos();
    }

    init(minDistance?: number, randomDistance?: number): void {
        super.init(minDistance, randomDistance);
    }

    setPos(minDistance = 0, randomDistance = 0, jumpIndex = 0) {
        this.init(minDistance, randomDistance);
        this.jumpIndex = jumpIndex;
    }

    // triggerStart() {
    //     if (this.isTrigger) return;
    //     this.isTrigger = true;
    // }

    onUpdate(): void {
        let cars = GameLogic.Share.carComps;
        for (let i = 0; i < cars.length; i++) {
            if (Math.abs(cars[i].progressDistance - this.progressDistance) < 3) {
                cars[i].jump(this.jumpIndex);
            }
        }
    }
}