import DropObj from "./DropObj";
import MyVector3 from "./MyVector3";


export default class DropItem extends DropObj {

    init(): void {
        super.init();
        this.dorpDeadY = this.self.transform.position.y - 2;
        this.isDestory = true;
        this.dropDeadChangeX = 0.65;
        this.isLandRotate = true;
        this.isActive = true;
        this.yforce = 0;
        this.changeDropData(new Laya.Vector2(10, 30), new Laya.Vector2(-10, 10), new Laya.Vector2(10, 15), new Laya.Vector3(1, 1, 1), new Laya.Vector3(this.randomMinMax(-10, 10), this.randomMinMax(-2, 2), this.randomMinMax(-2, 2)), 25);
    }
    isColliderActive: boolean = false;
    isLandRotate: boolean = true
    onUpdate() {
        let dt = Laya.timer.delta / 1000;
        //落地前效果
        if (this.isActive) {
            //掉落效果Update
            this.yforce -= this.gravity * dt;
            let self = this.self as Laya.Sprite3D;
            let model = this.self.getChildAt(0) as Laya.Sprite3D;
            if (self && !self.destroyed) {
                self.transform.translate(new Laya.Vector3(this.dir.x * this.xForce * dt, this.yforce * dt, this.dir.z * this.zForce * dt), true);
                model.transform.rotate(MyVector3.mull(this.rotate, dt), true)
                // //如果超过跑道两侧则掉落
                // if (Math.abs(this.self.transform.position.x) > this.dropDeadChangeX) {
                //     this.dorpDeadY = -19.5;
                //     this.isDestory = true;
                // }
                //如果超过掉落死亡线则销毁
                if (model.transform.position.y < this.dorpDeadY) {
                    if (this.isDestory)
                        this.self.destroy();
                    else {
                        self.transform.position = new Laya.Vector3(this.self.transform.position.x, 0, this.self.transform.position.z)
                        this.isActive = false;
                    }
                }
            }
        } else if (this.isLandRotate) {
            //落地后滚动效果，其实就是根据掉落后的速度，进行的rotate和速度衰减
            this.isColliderActive = true;
            let self = this.self as Laya.Sprite3D;
            let model = this.self.getChildAt(0) as Laya.Sprite3D;
            if (self && !self.destroyed) {
                let dir = this.dir.clone();
                Laya.Vector3.lerp(this.dir, new Laya.Vector3(), dt * 4, dir)
                if (dir.x < 0.1 && dir.z < 0.1) {
                    this.isLandRotate = false;
                }
                this.dir = dir;
                let isGravity = Math.abs(this.self.transform.position.x) > this.dropDeadChangeX + 0.1;
                if (!isGravity) {
                    self.transform.translate(new Laya.Vector3(this.dir.x * this.xForce * dt, 0, this.dir.z * this.zForce * dt), false);
                    model.transform.rotate(new Laya.Vector3(this.dir.z * this.zForce * dt * 0.5, 0, 0), false)
                } else {
                    this.yforce -= this.gravity * dt * 0.1;
                    self.transform.translate(new Laya.Vector3(this.dir.x * this.xForce * dt, this.yforce * dt, this.dir.z * this.zForce * dt), false);
                    model.transform.rotate(new Laya.Vector3(this.dir.z * this.zForce * dt * 0.5, 0, 0), false)
                    if (model.transform.position.y < -5) {
                        this.self.destroy();
                    }
                }
            }
        }
    }

    colliderDo() {
        if (this.isActive)
            return;
        // ItemsController.instance.addOldItem(this.self.getComponent(Item));
        // Platform.instance.vibrateShort();
        this.destroy();
    }

    randomMinMax(min, max) {
        return Math.random() * (max - min) + min;
    }
}