        // if (stageX != this.originPosX) {
        //     let pos = this.role.transform.localPosition.clone();
        //     let rot = this.roleChild.transform.localRotationEuler.clone();

        //     let dis = stageX - this.originPosX;
        //     // console.log(dis);
        //     pos.x -= this.moveFactor * dis * this.speedFactor;
